<template>
  <div>
    <!--    -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">  </h2>
      <div class="content-sub-title">
        <p>: [{{ filteredStartDate }} ~ {{ filteredEndDate }}]</p>
      </div>
      
      <!--   -->
      <DefaultButton 
        size="small" 
        align="left"
        color="gray"
        @click="goBack">
        
      </DefaultButton>

      <div class="d-flex align-items-center content-body-header">
        <span class="mr-2 all-click"> </span>
        <!-- <input 
          type="radio" 
          name="approveAll" 
          id="approveAll" 
          @change="toggleApproveAll" 
          v-model="selectAllDecision"
          value="approve" 
          class="mr-4" 
        /> -->
        <DefaultTextfield
          type="radio"
          name="approveAll" 
          id="approveAll"
          @change="toggleApproveAll" 
          v-model="selectAllDecision"
          value="approve" 
          size="small"
        />
        <span class="mr-2 all-click"> </span>
        <!-- <input 
          type="radio" 
          name="rejectAll" 
          id="rejectAll" 
          @change="toggleRejectAll" 
          v-model="selectAllDecision"
          value="reject" 
        /> -->
        <DefaultTextfield
          type="radio"
          name="rejectAll" 
          id="rejectAll"
          @change="toggleRejectAll" 
          v-model="selectAllDecision"
          value="reject" 
          size="small"
        />
      </div>
      <hr />

      <!--   (  ) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th> </th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in data"
            :key="index"
            @click="openSidebar(item)"
          >
            <td>{{ item.date }}</td>
            <td>{{ item.people.length }}</td>
            <td>{{ item.type }}</td>
            <td>{{ item.reason }}</td>
            <td>{{ formatAmount(item.amount) }}</td>
            <td>
              <a
                href="#"
                @click.prevent="openPreviewModal(item.receipt)"
                class="text-primary"
              >
                {{ item.receiptName }}
              </a>
            </td>
            <td>
              <input
                type="radio"
                :name="'decision' + index"
                class="approveRadio"
                v-model="item.decision"
                value="approve"
                @change="updateDecision()"
              />
            </td>
            <td>
              <input
                type="radio"
                :name="'decision' + index"
                class="rejectRadio"
                v-model="item.decision"
                value="reject"
                @change="updateDecision()"
              />
            </td>
            <td>
              <!-- <input
                type="text"
                class="form-control rejectReason"
                v-model="item.rejectReason"
              /> -->
              <DefaultTextfield
                size="full"
                v-model="item.rejectReason"
              />
            </td>
          </tr>
        </tbody>
      </table>

      <!--    (  ) -->
      <div class="card-layout" v-if="isMobile">
        <div
          class="card"
          v-for="(item, index) in data"
          :key="index"
        >
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text">
              <strong> : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">
                {{ item.people.length }}
              </span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header"></div>
                <p
                  v-for="person in item.people"
                  :key="person.name"
                  class="people-list-item"
                >
                  {{ person.name }} ({{ person.department }} - {{ person.team }})
                </p>
              </div>
              <hr />
            </div>
            <p class="card-text">
              <strong>/ : </strong>
              {{ item.type }} / {{ item.reason }}
            </p>
            <p class="card-text">
              <strong> : </strong>
              {{ formatAmount(item.amount) }}
            </p>
            <p class="card-text">
              <strong>/ : </strong>
              {{ calculateAmountPerPerson(item) }}
            </p>
            <p>
              <strong class="card-text">  : </strong>
              <a
                @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;"
              >
                {{ item.receiptName }}
              </a>
            </p>
            <p class="card-text">
              <strong>: </strong>
              
              <input
                type="radio"
                :name="'decision' + index"
                class="approveRadio"
                v-model="item.decision"
                value="approve"
                @change="updateDecision()"
              />
              | 
              <input
                type="radio"
                :name="'decision' + index"
                class="rejectRadio"
                v-model="item.decision"
                value="reject"
                @change="updateDecision()"
              />
              <br>
            </p>
            <p class="card-text d-flex align-items-center">
              <strong>: </strong>
              <input
                type="text"
                class="form-control rejectReason ml-2"
                v-model="item.rejectReason"
                style="flex: 1;"
              />
            </p>
          </div>
        </div>
      </div>

      <!--   -->
      <DefaultButton align="right" @click="save">
        
      </DefaultButton>

      <!--  ( ) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />

      <!--   -->
      <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
        <ReceiptSidebar 
          v-if="!isMobile" 
          :item="selectedItem" 
          :visible="sidebarVisible" 
          @close="closeSidebar"
          @preview-image="openPreviewModal"
        />
      </div>
      
      <!--    -->
      <div
        v-if="isPreviewVisible"
        class="modal preview-modal"
        @click="closePreviewModalOnOutsideClick"
      >
        <div
          class="preview-modal-content"
          @mousedown="startDrag"
          @mousemove="onDrag"
          @mouseup="endDrag"
          @mouseleave="endDrag"
          @touchstart="startDrag"
          @touchmove="onDrag"
          @touchend="endDrag"
        >
          <img
            :src="previewImage"
            :class="{ zoomed: isZoomed }"
            class="preview-modal-image"
            :style="{
              transform: isZoomed
                ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
                : 'none',
              transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
            }"
            @dblclick="toggleZoom"
            @touchstart="toggleZoom"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import ReceiptSidebar from './ReceiptSidebar.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
// import axios from 'axios'; //    

import { usePreviewModal } from '@/utils/preview-modal';
const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

//  / 
const selectedItem = ref(null);
const sidebarVisible = ref(false);
const isMobile = ref(window.innerWidth <= 1400);
function updateIsMobile() {
  isMobile.value = window.innerWidth <= 1400;
}

//   ( )
const filteredStartDate = ref('2024-01');
const filteredEndDate = ref('2024-03');

// 
const router = useRouter();
function goBack() {
  router.back();
}

//    
const data = ref([]);

// 
const currentPage = ref(1);
const totalPages = ref(1);
const visiblePageCount = ref(5);  //      

/**
 *   
 * -  axios  +  (page ) 
 * -   data totalPages 
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // const response = await axios.get('/api/some-endpoint', {
    //   params: { page },
    // });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    //  (mockData)
    const mockData = [
      {
        date: '2024-08-01',
        type: '',
        reason: '',
        amount: '100,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        rejectionReason: '',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      {
        date: '2024-08-02',
        type: '',
        reason: '',
        amount: '110,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        rejectionReason: '',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      // ...  
    ];
    // mock  
    data.value = mockData.map(item => ({
      ...item,
      decision: '', // /  
    }));

    totalPages.value = 3; //   3  
  } catch (err) {
    console.error(err);
  }
}

/**
 *   (  )
 * -  
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

//   
function save() {
  console.log('Data saved:', data.value);
  //  data.value    
}

// --------------------------------------------------------------------------------------
// / (  or  ) 
// --------------------------------------------------------------------------------------
const selectAllDecision = ref(''); //  / 
function toggleApproveAll() {
  if (selectAllDecision.value === 'approve') {
    data.value.forEach(item => { item.decision = 'approve'; });
  } else {
    data.value.forEach(item => { item.decision = ''; });
  }
}
function toggleRejectAll() {
  if (selectAllDecision.value === 'reject') {
    data.value.forEach(item => { item.decision = 'reject'; });
  } else {
    data.value.forEach(item => { item.decision = ''; });
  }
}
function updateDecision() {
  const allApproved = data.value.length > 0 && data.value.every(item => item.decision === 'approve');
  const allRejected = data.value.length > 0 && data.value.every(item => item.decision === 'reject');

  if (allApproved) {
    selectAllDecision.value = 'approve';
  } else if (allRejected) {
    selectAllDecision.value = 'reject';
  } else {
    selectAllDecision.value = '';
  }
}

// --------------------------------------------------------------------------------------
//  
// --------------------------------------------------------------------------------------
function openSidebar(item) {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
}
function closeSidebar() {
  sidebarVisible.value = false;
}
function closePreviewModalOnOutsideClick(event) {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
}

//  
function formatAmount(amountStr) {
  const amount = parseInt(amountStr.replace(/[^0-9]/g, ''), 10) || 0;
  return amount.toLocaleString();
}

//  
function calculateAmountPerPerson(item) {
  const amountNum = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people.length || 0;
  return peopleCount > 0
    ? Math.floor(amountNum / peopleCount).toLocaleString() + ' '
    : 'N/A';
}

//    
const visiblePeopleLists = ref({});
function togglePeopleList(item) {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
}
function showPeopleList(item) {
  return visiblePeopleLists.value[item.date];
}

// --------------------------------------------------------------------------------------
// 
// --------------------------------------------------------------------------------------
onMounted(() => {
  //    
  fetchDataFromServer(currentPage.value);

  //  
  window.addEventListener('resize', updateIsMobile);
});
</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.content {
  transition: margin-right 0.3s ease;
}

.content-sub-title {
  margin-bottom: 0px !important;
}

.content-expanded {
  margin-right: 300px;
}

.content-body-header {
  margin-top: 60px;
}

.text-primary {
  color: #c7573e;
  /*     */
  text-decoration: none;
  /*    */
}

/*  */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}

/*      */
.sidebar-visible {
  transform: translateX(0);
  /*    */
}

/*     */
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

/*  / */
.all-click {
  font-weight: bold;
  margin-left: 10px;
}

/*       */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}
.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}
.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

/*    */
.preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preview-modal-content {
  position: relative;
}
.preview-modal-image {
  max-width: 80vw;
  max-height: 80vh;
  transition: transform 0.2s ease;
}

@media (max-width: 1400px) {
  .page-link {
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }

  .page-item {
    font-size: 0.75rem;
  }

  .pagination {
    margin: 3px 0 60px 0;
  }
}

@media (max-width: 1200px) {
  .bnt-back {
    margin-bottom: 40px;
  }

  .rejectReason {
    width: 100%;
  }
}
</style>
