<template>
  <div>
    <!--    -->
    <div class="content">
      <h2 class="content-title"> </h2>
      <div class="content-sub-title">
        <div class="text-muted">Notice Management</div>
      </div>

      <div class="create-notice">
        <!-- ""  (isAdmin   ) -->
        <DefaultButton
          v-if="isAdmin"
          align="right"
          @click="showModal('create')"
        >
          
        </DefaultButton>
      </div>

      <!--   -->
      <!--    showModal('view', item) -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :rowClick="(item) => showModal('view', item)"
      />

      <!--  ( ) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>

    <!--   -->
    <NoticeIntegratedCreateModal
      v-if="isModalVisible"
      :isAdmin="isAdmin"
      :isCreate="isCreate"
      :form="form"
      @close="isModalVisible = false"
      @save="handleSave"
      @delete="handleDelete"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import NoticeIntegratedCreateModal from '@/components/hrm/NoticeIntegratedCreateModifyModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import NoticeIntegratedApi from '@/api/hrm/NoticeIntegratedApi';

// =====================
//  
// =====================
const isAdmin = ref(true);

// =====================
//  / & / 
// =====================
const isModalVisible = ref(false);
const isCreate = ref(true);

// =====================
//  
// =====================
const currentPage = ref(1);
const totalPages = ref(1);          //     
const visiblePageCount = ref(5);    //      

// =====================
//  
// =====================
const form = ref({
  id: null,
  title: '',
  author: '',
  date: '',
  content: '',
  cookieConsent: false,
});

// =====================
//    
// =====================
const data = ref([]);

// =====================
//  
// =====================
const columns = [
  { key: 'id',     label: ''  , width: 10 },
  { key: 'title',  label: ''  , width: 200 },
  { key: 'author', label: '', width: 30 },
  { key: 'date',   label: '', width: 30 },
];

/**
 *    
 * -> ( mock)   , totalPages  
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

/**
 * ( mock)   
 * -  axios.get(...)   { page }   
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  console.log('test');
  try {
    // const response = await NoticeIntegratedApi.getNotices('/api/notice', { params: { page } });
    const response = await NoticeIntegratedApi.getNotices();
    data.value = response.data.items;
    totalPages.value = response.data.totalPages;

    // =====================
    // : mockData
    // =====================
    const mockData = [
      { id: 1,  title: '  1',  author: 'A', date: '2024-08-01', content: '  1' },
      { id: 2,  title: '  2',  author: 'B', date: '2024-08-02', content: '  2' },
      { id: 3,  title: '  3',  author: 'C', date: '2024-08-03', content: '  3' },
      { id: 4,  title: '  4',  author: 'A', date: '2024-08-04', content: '  4' },
      { id: 5,  title: '  5',  author: 'D', date: '2024-08-05', content: '  5' },
      { id: 6,  title: '  6',  author: 'E', date: '2024-08-06', content: '  6' },
      { id: 7,  title: '  7',  author: 'F', date: '2024-08-07', content: '  7' },
      { id: 8,  title: '  8',  author: 'B', date: '2024-08-08', content: '  8' },
      { id: 9,  title: '  9',  author: 'C', date: '2024-08-09', content: '  9' },
      { id: 10, title: '  10', author: 'A', date: '2024-08-10', content: '  10' },
      //         
    ];
    //     & totalPages 
    data.value = mockData;
    totalPages.value = 3;  // :  3 
  } catch (error) {
    console.error(error);
  }
}

// =====================
//  
// =====================
function showModal(mode, item = null) {
  isCreate.value = (mode === 'create');
  if (isCreate.value) {
    //   ( )
    form.value = {
      id: null,
      title: '',
      author: '',
      date: getLocalDate(),
      content: '',
      cookieConsent: false,
    };
  } else {
    //  
    form.value = { ...item };
  }
  isModalVisible.value = true;
}

function getLocalDate() {
  const today = new Date();
  today.setMinutes(today.getMinutes() - today.getTimezoneOffset());
  return today.toISOString().split('T')[0];
}

/**
 * (/)
 * -   POST/PUT  ,  fetchDataFromServer()   
 */
function handleSave(updatedForm) {
  if (isCreate.value) {
    // 
    updatedForm.id = Date.now(); //  ID
    data.value.push(updatedForm);
  } else {
    // 
    const idx = data.value.findIndex(d => d.id === updatedForm.id);
    if (idx !== -1) {
      data.value[idx] = updatedForm;
    }
  }
  //  
  isModalVisible.value = false;
  fetchDataFromServer();
}

/**
 * 
 * -   DELETE    fetchDataFromServer()  
 */
function handleDelete(idToDelete) {
  if (idToDelete !== null) {
    data.value = data.value.filter(item => item.id !== idToDelete);
  }
  isModalVisible.value = false;
  fetchDataFromServer();
}

/**
 * onMounted  1  
 */
onMounted(() => {
  fetchDataFromServer(currentPage.value);
});
</script>

<style scoped>
/*  margin */
.pagination {
  margin-top: 30px !important;
}

/*   hover  */
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.create-notice {
  margin-bottom: 20px;
}
</style>
