<template>
  <div>
    <!--    -->
    <div class="content">
      <h2 class="content-title">  </h2>
      <p class="content-sub-title">    </p>

      <div class="d-flex justify-content-between align-items-center mb-3">
        <div class="d-flex align-items-center">
          <select class="form-control w-auto" v-model="selectedYear" @change="fetchData">
            <option>2024</option>
            <option>2023</option>
            <option>2022</option>
          </select>
          <label class="me-2">&nbsp; </label>
        </div>
      </div>

      <!-- DefaultTable   -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :footerData="footerData"
        :showTable="!isMobile"
      />
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';  //   import

//  
const selectedYear = ref('2024');
const data = ref([]);
const totalRequested = ref(0);
const totalApproved = ref(0);
const totalRejected = ref(0);
const totalPending = ref(0);
const isMobile = ref(false);  //     

//   
const columns = ref([
  { key: 'month', label: '', width: 50 },
  { key: 'requested', label: '', width: 100 },
  { key: 'approved', label: '', width: 100 },
  { key: 'rejected', label: '', width: 100 },
  { key: 'pending', label: '', width: 100 }
]);

//  
const dummyData = [
  { month: '1', requested: 1000000, approved: 800000, rejected: 100000, pending: 100000 },
  { month: '2', requested: 1200000, approved: 900000, rejected: 200000, pending: 100000  },
  { month: '3', requested: 800000, approved: 500000, rejected: 100000, pending: 200000  },
  { month: '4', requested: 1500000, approved: 1300000, rejected: 100000, pending: 100000   },
  { month: '5', requested: 1200000, approved: 1100000, rejected: 100000, pending: 200000   },
  //  ...
];

//   
const fetchData = () => {
  //   
  data.value = dummyData.filter(() => {
    //      
    return true;
  });

  //   
  totalRequested.value = data.value.reduce((sum, item) => sum + item.requested, 0);
  totalApproved.value = data.value.reduce((sum, item) => sum + item.approved, 0);
  totalRejected.value = data.value.reduce((sum, item) => sum + item.rejected, 0);
  totalPending.value = data.value.reduce((sum, item) => sum + item.pending, 0);

  //   
  footerData.value = [
    ' ',
    totalRequested.value.toLocaleString() + '',
    totalApproved.value.toLocaleString() + '',
    totalRejected.value.toLocaleString() + '',
    totalPending.value.toLocaleString() + ''
  ];
};

//   (  )
const footerData = ref([]);

//    
onMounted(fetchData);
</script>

<style scoped>
.content {
  margin-bottom: 50px;
}
</style>
