<template>
  <div>
    <!--   -->
    <div class="content">
      <h2 class="content-title">
        {{ searchNameResult ? `${searchNameResult}  ` : '   ' }}
      </h2>
      <p class="content-sub-title">    </p>

      <div class="d-flex justify-content-between align-items-center" style="color: red; font-weight: bold;">
        <p></p>
        <p>  "" </p>
      </div>

      <div class="d-flex justify-content-between align-items-start">
        <!--    -->
        <DefaultButton 
          v-if="isAdmin && searchNameResult"
          align="left"
          @click="openCreateModal"
        >
           
        </DefaultButton>

        <p></p>
        <!--   -->
        <div class="d-flex justify-content-between mb-3 search-body">
          <div>
            <input
              type="text"
              class="form-control input-search-name"
              v-model="searchName"
              placeholder=" "
              @keyup.enter="filterData"
            />
          </div>
          <DefaultButton @click="filterData">  </DefaultButton>
        </div>
      </div>

      <!--       -->
      <div v-if="!searchNameResult" class="d-flex justify-content-center mt-5">
        <img
          src="https://www.kpcnc.co.kr/src/img/main_slider1.png"
          alt="     "
          class="placeholder-image"
        />
      </div>

      <!--   ( 10  ) -->
      <DefaultTable
        v-if="searchNameResult"
        :columns="columns"
        :data="data"
        :rowClick="(item) => openEditModal(item)"
        :ellipsisWidth="50"
      />

      <PerformanceManagementCreateModal
        :visible="createModalVisible"
        :formData="formData"
        @close="createModalVisible = false"
        @submit="submitPerformance"
      />

      <!-- (2)  /   -->
      <PerformanceManagementDeleteModifyModal
        :visible="editModalVisible"
        :editDataProp="editData"
        @close="editModalVisible = false"
        @save="submitEdit"
        @delete="removeItem"
      />

      <!--   AlertModal -->
      <AlertModal
        :isVisible="resultModalVisible"
        :disableBackgroundClose="true"
        title=" "
        confirmText=""
        @confirm="resultModalVisible = false"
      >
        <template #body>
          <p>   .</p>
        </template>
      </AlertModal>
    </div>
  </div>
</template>


<script setup>
import { ref, computed } from 'vue';
import axios from 'axios';

import PerformanceManagementCreateModal from '@/components/hrm/PerformanceManagementCreateModal.vue';
import PerformanceManagementDeleteModifyModal from '@/components/hrm/PerformanceManagementDeleteModifyModal.vue';

import AlertModal from '@/components/common/modal/AlertModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';

/**   */
const resultModalVisible = ref(false);

//  
const searchName = ref('');
const searchNameResult = ref('');

//  
const data = ref([]); //   (  )
const editData = ref({}); //    

//  
const userRole = ref('admin');
const isAdmin = computed(() => userRole.value === 'admin');

// " "  
const createModalVisible = ref(false);
const formData = ref({
  fromDate: '',
  toDate: '',
  workType: '',
  performanceTitle: '',
  performance: '',
  status: ' ',
});

//  
const columns = [
  { key: 'fromDate',  label: '', useEllipsis: true,  ellipsisWidth: 50},
  { key: 'toDate',    label: '', useEllipsis: true,  ellipsisWidth: 50}, 
  { key: 'workType',  label: '', useEllipsis: true,  ellipsisWidth: 50 }, 
  { key: 'performanceTitle',  label: '', useEllipsis: true,  ellipsisWidth: 50 },
  { key: 'performance', label: '', useEllipsis: true,  ellipsisWidth: 50 },
  {
    key: 'status',
    label: '',
    useEllipsis: true,
    ellipsisWidth: 30,
    // (value)    
    customClass: (value) => {
      if (value === ' ') return 'text-red';
      if (value === '')   return 'text-green';
      if (value === ' ') return 'text-blue';
      return '';
    }
  },
];

// "/"  
const editModalVisible = ref(false);

/**   */
async function filterData() {
  if (!searchName.value.trim()) {
    alert('  .');
    data.value = [];
    searchNameResult.value = '';
    return;
  }
  // : ''  
  if (searchName.value === '') {
    data.value = [
      {
        fromDate: '2024-05-01',
        toDate: '2024-05-15',
        workType: '',
        performanceTitle: ' ',
        performance: '   ',
        status: '',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
    ];
    searchNameResult.value = '';
  } else {
    //  
    try {
      const response = await axios.get('/api/performance', {
        params: { name: searchName.value },
      });
      data.value = response.data;
      searchNameResult.value = searchName.value;
    } catch (error) {
      console.error(' :', error);
      resultModalVisible.value = true;
    }
  }
}

// (1)    
function openCreateModal() {
  //  
  formData.value = {
    fromDate: '',
    toDate: '',
    workType: '',
    performanceTitle: '',
    performance: '',
    status: ' ',
  };
  createModalVisible.value = true;
}

// (1')    ""
function submitPerformance(newForm) {
  console.log('Performance Submitted:', newForm);
  //   => data.value.push(newForm);
  createModalVisible.value = false;
}

// (2)   
function openEditModal(item) {
  editData.value = { ...item }; // 
  editModalVisible.value = true;
}
// (2')   ""
function submitEdit(updatedItem) {
  console.log('Performance Edited:', updatedItem);
  //   =>   
  editModalVisible.value = false;
}
// (2'')   ""
function removeItem(deletedItem) {
  console.log('Delete item:', deletedItem);
  data.value = data.value.filter((x) => x !== deletedItem);
  editModalVisible.value = false;
}

</script>

<style scoped>
/*    */
.table-container {
  max-height: 400px; /*   */
  overflow-y: auto;  /*   */
}
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close {
  font-size: 1.5rem;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.performance-save-button {
  background-color: #375a7f;
  font-size: 0.875rem;
  padding: 0.4rem 0.75rem;
}

