<template>
  <div v-if="visible" class="modal-overlay">
    <div class="modal-container">
      <!--   -->
      <div class="modal-header">
        <h5 class="modal-title"> </h5>
        <button type="button" class="btn-close" @click="closeModal" />
      </div>

      <!--   -->
      <div class="modal-body">
        <form @submit.prevent="submitEdit">
          <!-- ,  -->
          <div class="d-flex">
            <div class="form-group w-50 me-2">
              <label for="editFromDate"></label>
              <input
                type="date"
                class="form-control"
                id="editFromDate"
                v-model="editData.fromDate"
              />
            </div>
            <div class="form-group w-50">
              <label for="editToDate"></label>
              <input
                type="date"
                class="form-control"
                id="editToDate"
                v-model="editData.toDate"
              />
            </div>
          </div>
          <br />
          <!-- ,  -->
          <div class="d-flex">
            <div class="form-group me-2" style="flex: 4 3;">
              <label for="editWorkType"></label>
              <select class="form-control" id="editWorkType" v-model="editData.workType">
                <option></option>
                <option></option>
                <option> </option>
              </select>
            </div>
            <div class="form-group" style="flex: 4 1;">
              <label for="editStatus"></label>
              <select class="form-control" id="editStatus" v-model="editData.status">
                <option> </option>
                <option></option>
                <option> </option>
              </select>
            </div>
          </div>
          <br />
          <!--   -->
          <div class="form-group">
            <label for="editPerformanceTitle"> </label>
            <input
              type="text"
              class="form-control"
              id="editPerformanceTitle"
              placeholder="  "
              v-model="editData.performanceTitle"
            />
          </div>
          <br />
          <!--  -->
          <div class="form-group">
            <label for="editPerformance"></label>
            <textarea
              class="form-control"
              id="editPerformance"
              placeholder=" "
              v-model="editData.performance"
              rows="4"
            ></textarea>
          </div>
          <br />
          <!--  -->
          <div class="modal-footer">
            <DefaultButton 
              align="right"
              color="gray"
              @click="closeModal"
            >
              
            </DefaultButton>
            <DefaultButton 
              align="right"
              color="red"
              @click="showDeleteAlert"
            >
              
            </DefaultButton>
            <DefaultButton 
              align="right"
              @click="submitEdit"
            >
              
            </DefaultButton>
          </div>
        </form>
      </div>
    </div>
    <AlertModal 
      :isVisible="deleteConfirmVisible" 
      :disableBackgroundClose="true"
      title=" " 
      confirmText="" 
      cancelText=""
      @close="deleteConfirmVisible = false" 
      @confirm="deleteNotice">
      <template #body>
        <p>?</p>
      </template>
    </AlertModal>
  </div>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits } from 'vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import AlertModal from '@/components/common/modal/AlertModal.vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  editDataProp: {
    type: Object,
    default: () => ({
      fromDate: '',
      toDate: '',
      workType: '',
      performanceTitle: '',
      performance: '',
      status: ' ',
    }),
  },
});
const emits = defineEmits(['close', 'save', 'delete']);

/**
 *  
 */
const editData = ref({ ...props.editDataProp });

/**
 *  editDataProp  localEditData 
 */
watch(
  () => props.editDataProp,
  (newVal) => {
    editData.value = { ...newVal };
  },
  { deep: true }
);

/**   */
function closeModal() {
  emits('close');
}

/**  */
function submitEdit() {
  /** 
   * 
   *   ,     
   * 
   */
  //     
  emits('save', { ...editData.value });
}

const deleteConfirmVisible = ref(false);

/**    => AlertModal  */
function showDeleteAlert() {
  deleteConfirmVisible.value = true;
}

/** AlertModal   =>     */
function deleteNotice() {
  //   ID  
  deleteConfirmVisible.value = false;
  // ( AlertModal    )
  emits('delete', { ...editData.value });
}
</script>

<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1050;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-container {
  background: #fff;
  width: 100%;
  max-width: 600px;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.3s;
  padding: 1rem;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.modal-body {
  max-height: 70vh;
  overflow-y: auto;
}
.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
}

/*    */
.d-flex {
  display: flex;
  gap: 0.5rem;
}
.me-2 {
  margin-right: 0.5rem;
}
.w-50 {
  width: 50%;
}
.form-control {
  width: 100%;
  padding: 0.4rem;
  margin: 0;
}

@media (max-width: 1200px) {
  .modal-container {
    width: 80% !important;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
</style>
