<template>
  <!-- b-modal:    v-model   -->
  <!-- b-modal OK         -->
  <!-- @ok.prevent="onSaveClick"( )  -->
  <!-- onSaveClick()   innerModalVisible.value = false;       -->
  <b-modal
    v-model="innerModalVisible"
    title="  "
    @ok.prevent="onSaveClick"
    ok-title=""
    cancel-title=""
    no-close-on-backdrop
    centered
  >
    <b-form @change="handleChange">
      <!-- ,  -->
      <b-form-group label="/" label-for="userDetails" class="user-detail-group">
        <div id="userDetails" class="d-flex">
          <b-form-input
            v-model="tempUser.name"
            :disabled="true"
            placeholder=""
            class="mr-2"
          ></b-form-input>
          <b-form-input
            id="userBirthDate"
            type="date"
            v-model="tempUser.birthDate"
            :disabled="true"
          ></b-form-input>
        </div>
      </b-form-group>

      <!--  -->
      <b-form-group label="" label-for="userEmail" class="user-detail-group">
        <div id="userEmail" class="d-flex">
          <b-form-input
            v-model="tempUser.email"
            placeholder=""
            class="mr-2"
          ></b-form-input>
        </div>
      </b-form-group>

      <!--     -->
      <b-form-group label="/ " label-for="userPassword" class="user-detail-group">
        <div id="userPassword" class="d-flex">
          <b-form-input
            v-model="tempUser.password"
            placeholder=""
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="passwordCheck"
            placeholder=" "
            class="mr-2"
          ></b-form-input>
        </div>
        <!--     -->
        <p v-if="isPasswordMismatch" class="userPasswordNotMatch">    .</p>
      </b-form-group>

      <!-- , / -->
      <b-form-group label="//" label-for="employmentDetails" class="user-detail-group">
        <div id="employmentDetails" class="d-flex">
          <b-form-select
            v-model="tempUser.employmentType"
            :options="['', '']"
            :disabled="!isAdmin"
            class="mr-2"
          ></b-form-select>
          <b-form-select
            v-model="tempUser.status"
            :options="['', '']"
            class="mr-2"
          ></b-form-select>
          <b-form-input
            type="date"
            v-model="computedDate"
            :disabled="true"
          ></b-form-input>
        </div>
      </b-form-group>

      <!-- , ,    -->
      <b-form-group label="//" label-for="userEmploymentDetails" class="user-detail-group">
        <div id="userEmploymentDetails" class="d-flex">
          <b-form-input
            v-model="tempUser.department"
            :disabled="!isAdmin"
            placeholder=""
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="tempUser.team"
            :disabled="!isAdmin"
            placeholder=""
            class="mr-2"
          ></b-form-input>
          <b-form-input
            v-model="tempUser.position"
            :disabled="!isAdmin"
            placeholder=""
          ></b-form-input>
        </div>
      </b-form-group>

      <!--    -->
      <b-form-group label="/" label-for="userZipCode" class="user-detail-group">
        <div id="userAddress" class="d-flex">
          <b-form-input
            v-model="tempUser.zipCode"
            :disabled="true"
            placeholder=""
            class="mr-2"
            style="width: 33%"
          ></b-form-input>
        </div>
        <b-form-input
          v-model="tempUser.address"
          :disabled="true"
          placeholder=""
          style="width: 100%"
        ></b-form-input>
      </b-form-group>

      <!--   -->
      <b-form-group label=" " label-for="userLocation" class="user-detail-group">
        <b-form-input
          id="userLocation"
          v-model="tempUser.location"
          :disabled="!isAdmin"
          placeholder=" "
        ></b-form-input>
      </b-form-group>
    </b-form>
  </b-modal>
</template>

<script setup>
import { ref, computed, watch } from 'vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  isAdmin: { type: Boolean, default: false },
  user: {
    type: Object,
    default: () => ({
      name: '',
      email: '',
      password: '',
      department: '',
      team: '',
      position: '',
      joinDate: '',
      leaveDate: '',
      status: '',
      birthDate: '',
      employmentType: '',
      zipCode: '',
      address: '',
      location: ''
    })
  }
});

const emits = defineEmits(['close', 'confirmSave']);

// b-modal    ref
const innerModalVisible = ref(props.visible);

//     
const tempUser = ref({ ...props.user });

//  
const passwordCheck = ref(props.user.password);
const isPasswordMismatch = ref(false);

/**
 *    
 */
watch(
  () => props.visible,
  (newVal) => {
    innerModalVisible.value = newVal;
    //    tempUser 
    if (newVal) {
      tempUser.value = { ...props.user };
      passwordCheck.value = props.user.password;
      isPasswordMismatch.value = false;
    }
  }
);

watch(
  () => innerModalVisible.value,
  (newVal) => {
    //   ,  close 
    if (!newVal) {
      emits('close');
    }
  }
);

/**
 * /    
 */
const computedDate = computed({
  get() {
    return tempUser.value.status === ''
      ? tempUser.value.joinDate
      : tempUser.value.leaveDate;
  },
  set(value) {
    if (tempUser.value.status === '') {
      tempUser.value.joinDate = value;
    } else {
      tempUser.value.leaveDate = value;
    }
  }
});

/**
 *   
 */
function handleChange() {
  //   
  isPasswordMismatch.value = (tempUser.value.password !== passwordCheck.value);
}

/**
 *   (b-modal @ok)  
 */
function onSaveClick() {
  //    
  if (isPasswordMismatch.value) {
    //  
    emits('confirmSave', {
      isMismatch: true,
      isChanged: false,
      userData: tempUser.value,
      statusChangedFlag: false
    });
    return;
  }

  //     
  const original = JSON.stringify(props.user);
  const current = JSON.stringify(tempUser.value);
  const isChanged = (original !== current);

  //    (/)
  const statusChangedFlag = (tempUser.value.status !== props.user.status);

  //    
  emits('confirmSave', {
    isMismatch: false,
    isChanged,
    userData: tempUser.value,
    statusChangedFlag
  });
}
</script>

<style scoped>
/*   , ,    CSS */
/* (BootstrapVue .modal-*        .) */

/*    */
.userPasswordNotMatch {
  color: red;
}
/*  (  ) */
.user-detail-group {
  margin-bottom: 10px !important;
}
/*   */
#userAddress {
  margin-bottom: 10px;
}

/*    BootstrapVue  
    .modal, .modal-dialog, .modal-content    .

        ()
   .modal-dialog {
     max-width: 400px;
   }
*/
</style>
