<template>
  <!--   -->
  <!-- onMounted     -->
  <!-- data-bs-backdrop="static" :     -->
  <!-- data-bs-keyboard="false" : ESC    -->
  <div 
    class="modal fade" 
    id="noticeModal" 
    tabindex="-1" 
    aria-labelledby="noticeModalLabel" 
    aria-hidden="true"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <!--   -->
        <div class="modal-header">
          <h5 class="modal-title" id="noticeModalLabel"> </h5>
          <button 
            type="button" 
            class="btn-close" 
            data-bs-dismiss="modal" 
            aria-label="Close"
            @click="closeModal"
          ></button>
        </div>

        <!--   -->
        <div class="modal-body">
          <form>
            <div class="mb-3">
              <label for="noticeTitle" class="form-label"></label>
              <input 
                type="text" 
                class="form-control" 
                id="noticeTitle" 
                v-model="localForm.title" 
                :disabled="!props.isAdmin" 
                placeholder=" "
              >
            </div>
            <div class="mb-3">
              <label for="noticeAuthor" class="form-label"></label>
              <input 
                type="text" 
                class="form-control" 
                id="noticeAuthor" 
                v-model="localForm.author" 
                :disabled="!props.isAdmin"
                placeholder=" "
              >
            </div>
            <div class="mb-3">
              <label for="noticeDate" class="form-label"></label>
              <input 
                type="date" 
                class="form-control" 
                id="noticeDate" 
                v-model="localForm.date" 
                disabled
              >
            </div>
            <div class="mb-3">
              <label for="noticeContent" class="form-label"></label>
              <textarea 
                class="form-control" 
                id="noticeContent" 
                v-model="localForm.content" 
                :disabled="!props.isAdmin" 
                rows="4" 
                placeholder=" "
              ></textarea>
            </div>
            <div v-if="!props.isAdmin" class="mb-3 form-check">
              <input 
                type="checkbox" 
                class="form-check-input" 
                id="cookieConsent" 
                v-model="localForm.cookieConsent"
              >
              <label class="form-check-label" for="cookieConsent">
                    (  )
              </label>
            </div>
          </form>
        </div>

        <!--   -->
        <div class="modal-footer">
          <button 
            type="button" 
            class="btn btn-secondary" 
            data-bs-dismiss="modal" 
            @click="closeModal"
          >
            
          </button>
          <!--   (isCreate === true) -->
          <button 
            type="button" 
            class="btn btn-primary" 
            v-if="props.isAdmin && props.isCreate" 
            @click="saveNotice"
          >
            
          </button>
          <!--   (isCreate === false) -->
          <button 
            type="button" 
            class="btn btn-primary" 
            v-if="props.isAdmin && !props.isCreate" 
            @click="saveNotice"
          >
            
          </button>
          <!--   (isCreate === false) -->
          <button 
            type="button" 
            class="btn btn-danger" 
            v-if="props.isAdmin && !props.isCreate"
            @click="showDeleteAlert"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>

  <!--   AlertModal (  ) -->
  <AlertModal 
    :isVisible="deleteConfirmVisible" 
    :disableBackgroundClose="false"
    title=" " 
    confirmText="" 
    cancelText=""
    @close="deleteConfirmVisible = false" 
    @confirm="deleteNotice"
  >
    <template #body>
      <p>?</p>
    </template>
  </AlertModal>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits, onMounted } from 'vue';
import * as bootstrap from 'bootstrap';
import AlertModal from '@/components/common/modal/AlertModal.vue';

/**
 *   props
 *  - isAdmin:  
 *  - isCreate: / 
 *  - form:   
 */
const props = defineProps({
  isAdmin: Boolean,
  isCreate: Boolean,
  form: Object,
});

/**
 * :
 *  - close:  
 *  - save: /    
 *  - delete:     
 */
const emit = defineEmits(['close', 'save', 'delete']);

/** localForm:  form   */
const localForm = ref({ ...props.form });
watch(
  () => props.form,
  (newForm) => {
    localForm.value = { ...newForm };
  },
  { deep: true }
);

/**   &      */
const modalInstance = ref(null);
const deleteConfirmVisible = ref(false);

/** onMounted Bootstrap   &  */
onMounted(() => {
  const modalElement = document.getElementById('noticeModal');
  modalInstance.value = new bootstrap.Modal(modalElement, {
    // backdrop: 'static', //    
    // keyboard: false     // ESC   
  });
  //  
  modalInstance.value.show();
});

/**   */
function closeModal() {
  modalInstance.value.hide();
  emit('close');
}

/** /  */
function saveNotice() {
  // localForm   
  emit('save', { ...localForm.value });
  modalInstance.value.hide();
  emit('close');
}

/**    => AlertModal  */
function showDeleteAlert() {
  deleteConfirmVisible.value = true;
}

/** AlertModal   =>     */
function deleteNotice() {
  //   ID  
  emit('delete', localForm.value.id);
  deleteConfirmVisible.value = false;
  modalInstance.value.hide();
  emit('close');
}
</script>

<style scoped>
.modal-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
}
</style>
