<template>
  <div>
    <!--    -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title"> </h2>
      <p class="content-sub-title">  </p>

      <div class="create-receipt">
        <DefaultButton 
          align="right"
          @click="openModal">
           
        </DefaultButton>
      </div>

      <!--    -->
      <ReceiptSubmissionCreateModal @close="closeModal" />

      <!--   -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :openSidebar="openSidebar"
        :showTable="!isMobile"
      />

      <!--   () -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="(item, idx) in data" :key="idx">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong> : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}</span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header"></div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{
                  person.department }} - {{ person.team }})</p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>/ : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong> : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>/ : </strong> {{ calculateAmountPerPerson(item) }}</p>
            <p>
              <strong class="card-text">  : </strong>
              <a class="card-text"
                @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;"
              >
                {{ item.receiptName }}
              </a>
            </p>
          </div>
        </div>
      </div>

      <!--  ( ) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>

    <!--  -->
    <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
      <ReceiptSidebar 
        v-if="!isMobile" 
        :item="selectedItem" 
        :visible="sidebarVisible" 
        @close="closeSidebar"
        @preview-image="openPreviewModal"
      />
    </div>

    <!--    -->
    <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
      <div
        class="preview-modal-content"
        @mousedown="startDrag"
        @mousemove="onDrag"
        @mouseup="endDrag"
        @mouseleave="endDrag"
        @touchstart="startDrag"
        @touchmove="onDrag"
        @touchend="endDrag"
      >
        <img
          :src="previewImage"
          :class="{ zoomed: isZoomed }"
          class="preview-modal-image"
          :style="{
            transform: isZoomed
              ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
              : 'none',
            transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
          }"
          @dblclick="toggleZoom"
          @touchstart="toggleZoom"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import * as bootstrap from 'bootstrap';
import ReceiptSidebar from './ReceiptSidebar.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import ReceiptSubmissionCreateModal from '@/components/receipt/ReceiptSubmissionCreateModal.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';

import { usePreviewModal } from '@/utils/preview-modal';
// import axios from 'axios'; //     

const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

// 
const selectedItem = ref(null);
const sidebarVisible = ref(false);

//  
const isMobile = ref(window.innerWidth <= 1200);
const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1200;
};

//   
const currentPage = ref(1);
const totalPages = ref(1); //     
const visiblePageCount = ref(5); //    

//  
const data = ref([]); //    (  )
const columns = [
  { key: 'date', label: '', width: 100  },
  { key: 'type', label: '', width: 50  },
  { key: 'peopleCount', label: '', width: 60  },
  { key: 'reason', label: '', width: 200  },
  { key: 'amount', label: '', width: 60  },
  { key: 'amountPerPerson', label: '/', width: 100  }
];

/** 
 *     
 * -  page( offset )     .
 * -     totalPages( totalItems)  state 
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

/**
 *    ()
 * -  axios   page, itemsPerPage  
 *     /totalPages  .
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // : axios.get('/api/receipts', { params: { page, itemsPerPage } })
    // const response = await axios.get('/api/receipts', { params: { page, itemsPerPage } });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    //    
    const mockData = [
      {
        date: '2024-08-01',
        type: '',
        reason: '',
        amount: '100,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' }
        ],
      },
      {
        date: '2024-08-02',
        type: '',
        reason: '',
        amount: '120,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      {
        date: '2024-08-02',
        type: '',
        reason: '',
        amount: '120,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      // ...
    ];

    //      ,   
    data.value = mockData.map(item => ({
      ...item,
      peopleCount: item.people.length,
      amountPerPerson: calculateAmountPerPerson(item)
    }));

    //    (  )
    totalPages.value = 3;
  } catch (e) {
    console.error(e);
  }
}

// /  
function calculateAmountPerPerson(item) {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people.length;
  return peopleCount > 0
    ? Math.floor(amount / peopleCount).toLocaleString() + ''
    : 'N/A';
}

//    
const visiblePeopleLists = ref({});
const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};
const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

//    
const modalInstance = ref(null);
function openModal() {
  const modalElem = document.getElementById('receiptModal');
  if (!modalInstance.value) {
    modalInstance.value = new bootstrap.Modal(modalElem, {
      backdrop: 'static',
      keyboard: false,
    });
  }
  modalInstance.value.show();
}
function closeModal() {
  modalInstance.value?.hide();
}
function openSidebar(item) {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
}
function closeSidebar() {
  sidebarVisible.value = false;
}

//   
onMounted(() => {
  window.addEventListener('resize', updateIsMobile);

  //    1  
  fetchDataFromServer(currentPage.value);
});

//    
function closePreviewModalOnOutsideClick(event) {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
}
</script>

<style scoped>
.content {
  transition: margin-right 0.3s ease;
}

.content-expanded {
  margin-right: 300px;
}

.create-receipt {
  margin-bottom: 20px;
}

/*   */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}
.sidebar-visible {
  transform: translateX(0);
}
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  overflow-y: auto;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
}

/*    */
.preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  z-index: 9999;
  /*   */
  /* cursor: zoom-out; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview-modal-content {
  position: relative;
}

.preview-modal-image {
  max-width: 80vw;
  max-height: 80vh;
  /*   */
  /* cursor: zoom-in; */
  transition: transform 0.2s ease;
}

.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

@media (max-width: 1200px) {
  .form-group {
    margin-bottom: 10px;
  }

  .form-group .mt-2 {
    margin: 0 !important;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .form-control-file {
    padding: 0px;
  }
}
</style>
