<template>
  <div>
    <!--  -->
    <div v-if="visible" class="sidebar">
      <button class="close-btn" @click="closeSidebar">&times;</button>
      <h5><strong>  </strong></h5>
      <hr />
      <p v-if="item.date"><strong> : </strong> {{ item.date }}</p>
      <p v-if="item.type"><strong> : </strong> {{ item.type }}</p>
      <p v-if="item.reason"><strong> : </strong> {{ item.reason }}</p>
      <p v-if="item.amount"><strong> : </strong> {{ item.amount }}</p>
      <p v-if="item.amount && item.people.length > 0"><strong>/ : </strong> {{ calculateAmountPerPerson(item) }}</p>
      <p v-if="item.receiptName">
        <strong>  : </strong>
        <!-- <a @click.prevent="openPreviewModal" style="cursor: pointer; color: blue;">{{ item.receiptName }}</a> -->
        <a @click.prevent="$emit('preview-image', item.receipt)" style="cursor: pointer; color: blue;">{{ item.receiptName }}</a>
      </p>
      <p v-if="item.status" :class="getStatusClass(item.status)"><strong class="status-text">  : </strong>{{ item.status }}</p>
      <p v-if="item.rejectionReason"><strong>  : </strong> {{ item.rejectionReason }}</p>
      <hr/>
      <p v-if="item.people && item.people.length"><strong> : </strong> {{ item.people.length }}</p>
      <ul v-if="item.people && item.people.length" class="people-list">
        <li class="people-list-item" v-for="(person, index) in item.people" :key="index">{{ person.department }} | {{ person.team }} | {{ person.name }} {{ person.position }}</li>
      </ul>
    </div>

    <!--    -->
    <div v-if="isPreviewVisible" class="modal" @click="closePreviewModalOnOutsideClick">
      <div class="modal-content" @click.stop>
        <img :src="item.receipt" class="modal-image" />
        <span class="close" @click="closePreviewModal">&times;</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';

defineProps({
  item: Object,
  visible: Boolean,
});

const emit = defineEmits(['close', 'preview-image']);
// const emit = defineEmits(['close']);
const isPreviewVisible = ref(false);

// / 
const calculateAmountPerPerson = (item) => {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10); //   
  const peopleCount = item.people.length;
  return peopleCount > 0 ? Math.floor(amount / peopleCount).toLocaleString() + ' ' : 'N/A';
};

const getStatusClass = (status) => {
  if (status === '') return 'status-pending';
  if (status === '') return 'status-approved';
  if (status === '') return 'status-rejected';
  return '';
};

const closeSidebar = () => {
  emit('close');
};

// const openPreviewModal = () => {
//   isPreviewVisible.value = true;
// };

const closePreviewModal = () => {
  isPreviewVisible.value = false;
};

const closePreviewModalOnOutsideClick = (event) => {
  //       .
  if (event.target.classList.contains('modal')) {
    isPreviewVisible.value = false;
  }
};
</script>

