  padding: 20px;
  transition: all 0.3s ease;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  overflow-y: auto; /*     */
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  background: none;
  border: none;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2000;
}

.modal-content {
  position: relative;
  width: 80%;
  max-width: 500px;
  background-color: transparent; /*   */
  padding: 0;
  text-align: center;
}

.modal-image {
  width: 100%;
  height: auto;
}

.close {
  position: absolute;
  top: -50px; /*   X  */
  right: -30px; /*   X  */
  font-size: 40px;
  cursor: pointer;
  background: none;
  border: none;
  color: white; /*   */
}

/*     */
.people-list {
  font-size: 0.75rem;
  max-height: 300px; /*    */
  overflow-y: auto; /*    */
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.people-list-item {
  margin: 7px 0 7px 0;
}

.status-approved {
  color: green;
  font-weight: bold;
}

.status-pending {
  color: blue;
  font-weight: bold;
}

.status-rejected {
  color: red;
  font-weight: bold;
}

