<template>
  <div :class="alignmentClass">
    <button
      :type="type"
      :class="['btn', buttonClass, customClass]"
      @click="handleClick"
    >
      <slot />
    </button>
  </div>
</template>

<script setup>
import { defineProps, defineEmits, computed } from 'vue';

const props = defineProps({
  type: {
    type: String,
    default: 'button', //  button
    validator: (value) => ['button', 'submit', 'reset'].includes(value),
  },
  size: {
    type: String,
    default: 'medium',
    validator: (value) => ['xsmall', 'small', 'medium', 'large'].includes(value),
  },
  customClass: {
    type: String,
    default: '',
  },
  align: {
    type: String,
    default: 'center',
    validator: (value) => ['left', 'center', 'right'].includes(value),
  },
  color: {
    type: String,
    default: 'default', //  
    validator: (value) => ['default', 'blue', 'red', 'green', 'yellow', 'gray'].includes(value),
  },
});

const emits = defineEmits(['click']);

const handleClick = (event) => {
  emits('click', event);
};

const buttonClass = computed(() => {
  let sizeClass = '';
  switch (props.size) {
    case 'xsmall':
      sizeClass = 'btn-xs'; 
      break;
    case 'small':
      sizeClass = 'btn-sm';
      break;
    case 'large':
      sizeClass = 'btn-lg';
      break;
    case 'medium':
    default:
      sizeClass = '';
      break;
  }
  const colorClass = `btn-${props.color}`;
  return `${sizeClass} ${colorClass}`;
});

const alignmentClass = computed(() => {
  switch (props.align) {
    case 'left':
      return 'text-start';
    case 'right':
      return 'text-end';
    default:
      return 'text-center';
  }
});
</script>

<style scoped>
.btn {
  --bs-btn-color: #fff;
  --bs-btn-bg: #375a7f; /*   */
  --bs-btn-border-color: #375a7f;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #5a7694;
  --bs-btn-hover-border-color: #5a7694;
  --bs-btn-focus-shadow-rgb: 60, 153, 110;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #5a7694;
  --bs-btn-active-border-color: #5a7694;
  --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #5a7694;
  --bs-btn-disabled-border-color: #5a7694;
  /* margin-bottom: 16px; */
}

/*   */
.btn-default {
  --bs-btn-bg: #375a7f;
  --bs-btn-border-color: #375a7f;
  --bs-btn-hover-bg: #5a7694;
  --bs-btn-hover-border-color: #5a7694;
}

.btn-blue {
  --bs-btn-bg: #007bff;
  --bs-btn-border-color: #007bff;
  --bs-btn-hover-bg: #0056b3;
  --bs-btn-hover-border-color: #0056b3;
}

.btn-red {
  --bs-btn-bg: #dc3545;
  --bs-btn-border-color: #dc3545;
  --bs-btn-hover-bg: #bd2130;
  --bs-btn-hover-border-color: #bd2130;
}

.btn-green {
  --bs-btn-bg: #28a745;
  --bs-btn-border-color: #28a745;
  --bs-btn-hover-bg: #218838;
  --bs-btn-hover-border-color: #218838;
}

.btn-yellow {
  --bs-btn-bg: #ffc107;
  --bs-btn-border-color: #ffc107;
  --bs-btn-hover-bg: #e0a800;
  --bs-btn-hover-border-color: #e0a800;
}

.btn-gray {
  --bs-btn-bg: #7c7c7c;
  --bs-btn-border-color: #7c7c7c;
  --bs-btn-hover-bg: #7c7c7c;
  --bs-btn-hover-border-color: #7c7c7c;
}

.btn-xs {
  padding: 0.2rem 0.45rem;
  font-size: 0.7rem;
  line-height: 1.2;
  height: 2rem;
}

.btn-sm {
  padding: 0.3rem 0.6rem;
  font-size: 0.8rem;
}

.btn-lg {
  padding: 0.4rem 0.95rem;
  font-size: 1rem;
}

.text-start {
  text-align: left !important;
}

.text-end {
  text-align: right !important;
}

.text-center {
  text-align: center !important;
}

@media (max-width: 1200px) {
  .btn {
    padding: 0.3rem 0.6rem !important;
    font-size: 0.75rem !important;
  /* margin-bottom: 7px; */
  }

  .btn-xs {
    padding: 0.15rem 0.3rem !important;
    font-size: 0.6rem !important;
    height: 2rem !important;
  }

  .btn-sm {
    padding: 0.2rem 0.4rem !important;
    font-size: 0.6rem !important;
  }

  .btn-lg {
    padding: 0.4rem 0.7rem !important;
    font-size: 0.8rem !important;
  }
}

</style>
