<template>
  
  <!--
    Pinia useLoadingStore     isLoading  
    Axios  startLoading() / stopLoading()   API   
    Router (beforeEach / afterEach)    
      (App.vue )  vue3-loading-overlay  isLoading true  

    :active="isLoading"
      true   , false 
    opacity, backgroundColor
          
    loader, color
       (spinner, dots, ) 
    zIndex
           
  -->
  <!--    -->
  <LoadingOverlay
    :active="isLoading"
    :opacity="0.8"
    :backgroundColor="'#ffffff'"
    :loader="'spinner'"
    :color="'#375a7f'"
    :zIndex="9999"
  />

  <div>
    <!-- v-if        -->
    <Header v-if="!(isLoginPage || isJoinPage)" />
    <Navigation v-if="!(isLoginPage || isJoinPage)" />
    <router-view />
    <Footer v-if="!(isLoginPage || isJoinPage)" />
  </div>
</template>

<script setup>
import { computed } from 'vue';
import { useRoute } from 'vue-router';
import Header from './components/HeaderLayout.vue';
import Footer from './components/FooterLayout.vue';
import Navigation from './components/NavigationLayout.vue';

import LoadingOverlay from 'vue3-loading-overlay';
import { useLoadingStore } from '@/store/loading';
import 'vue3-loading-overlay/dist/vue3-loading-overlay.css';

//   
const loadingStore = useLoadingStore();
const isLoading = computed(() => loadingStore.isLoading);

const route = useRoute();

//         
const isLoginPage = computed(() => route.meta.isLoginPage);
const isJoinPage = computed(() => route.meta.isJoinPage);
</script>

