<template>
  <!--   () -->
  <div v-if="visible" class="modal-backdrop">
    <!--   -->
    <div class="modal-container">
      <div class="modal-header">
        <span class="modal-title"> </span>
        <button class="modal-close" @click="closeModal"></button>
      </div>
      <div class="modal-body">
        <!--     -->
        <!--  div   embed(iFrame)   -->
        <div
          ref="postcodeContainer"
          style="width: 100%; height: 100%;"
          class="scaled-postcode" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, nextTick } from 'vue';

const props = defineProps({
  visible: Boolean
});

const emits = defineEmits(['close', 'selectAddress']);

/**
 *      div
 */
const postcodeContainer = ref(null);

/**
 *       embed
 */
watch(() => props.visible, async (newVal) => {
  if (newVal) {
    await nextTick();
    openPostcodePopup();
  }
});

/**
 *     
 */
function openPostcodePopup() {
  if (!window.daum?.Postcode) {
    console.error('    .');
    return;
  }

  const postcode = new window.daum.Postcode({
    oncomplete(data) {
      emits('selectAddress', {
        address: data.address,
        zonecode: data.zonecode
      });
      closeModal();
    },
    onresize() {
      //    
    },
  });

  //   embed ->   
  postcode.embed(postcodeContainer.value, {
    width: '100%',
    height: '100%'
  });
}

function closeModal() {
  emits('close');
}
</script>

<style scoped>
.modal-backdrop {
  position: fixed;
  inset: 0; /* top:0, left:0, right:0, bottom:0   */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1050; /*    */
}

.modal-container {
  width: 515px;
  height: 560px;
  margin: 50px auto;
  background: #fff;
  position: relative;
  top: 50px;
  padding: 5px 5px 5px 5px;
  display: flex;
  flex-direction: column;
  z-index: 1100; /*       */
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: #F2F2F2; /*    */
}

.modal-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.modal-body {
  /* iFrame    overflow  auto */
  flex: 1;
  overflow: hidden; /*  auto.    */
  position: relative;
}

@media (max-width: 1200px) {
  .modal-title {
    font-size: 0.9rem;
    font-weight: bold;
  }

  .modal-header {
    padding: 7px 10px;
  }

  .modal-container {
    width: 410px;
    height: 450px;
    padding: 5px;
  }

  .scaled-postcode {
    transform: scale(0.8);
    transform-origin: top left;
  }
}

@media (max-width: 500px) {
  .modal-title {
    font-size: 0.65rem;
    font-weight: bold;
  }

  .modal-header {
    padding: 5px 7px;
  }

  .modal-container {
    width: 305px;
    height: 345px;
    padding: 3px;
  }

  .scaled-postcode {
    transform: scale(0.6);
    transform-origin: top left;
  }
}
</style>
