<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header text-center">
            <img src="https://www.kpcnc.co.kr/src/img/logo.png" class="logo mb-3" alt="logo">
          </div>
          <div class="card-body">
            <!--
                 
                Vue    ref   DOM  , 
                   ref ( formRef) 
                 formRef.value  <form> DOM  

              DOM    
                HTML <form>   checkValidity() reportValidity() 
                   .
                formRef.value        
            -->
            <form ref="formRef" @submit.prevent="handleSubmit">

              <!--    -->
              <div class="form-group custom-grid">
                <div>
                  <label for="userName"></label>
                  <DefaultTextfield
                    id="userName"
                    v-model="userName"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="phone"> </label>
                  <DefaultTextfield
                    id="phone"
                    v-model="phone"
                    size="full"
                    placeholder="'-'  "
                    required
                  />
                </div>
              </div>

              <!--  +  /    -->
              <div class="form-group">
                <div>
                  <label for="email"></label>
                  <div style="display: flex; align-items: center;">
                    <DefaultTextfield
                      id="email"
                      v-model="email"
                      size="full"
                      validationType="email"
                      required
                    />
                    <DefaultButton
                      color="gray"
                      @click="sendEmailVerification"
                    >
                        
                    </DefaultButton>
                  </div>
                </div>
              </div>
              <!--   +  (  ) -->
              <div class="form-group">
                <div style="display: flex; align-items: center;">
                  <DefaultTextfield
                    id="verificationCode"
                    v-model="verificationCode"
                    size="small"
                    :disabled="verificationCodeDisable"
                    placeholder=" "
                  />
                  <DefaultButton
                    color="yellow"
                    @click="sameVerification"
                  >
                     
                  </DefaultButton>
                </div>
              </div>

              <!--    -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="password"></label>
                  <!-- <input type="password" class="form-control" id="password" v-model="password" required> -->
                  <DefaultTextfield
                    type="password"
                    id="password"
                    v-model="password"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="confirmPassword"> </label>
                  <DefaultTextfield
                    type="password"
                    id="confirmPassword"
                    v-model="confirmPassword"
                    size="full"
                    required
                  />
                </div>
              </div>

              <!--   -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="dob"></label>
                  <DefaultTextfield
                    type="date"
                    id="dob"
                    v-model="dob"
                    size="full"
                    required
                  />
                </div>
                <div>
                  <label for="joinDate"></label>
                  <DefaultTextfield
                    type="date"
                    id="joinDate"
                    v-model="joinDate"
                    size="full"
                    required
                  />
                </div>
              </div>

              <!-- , ,  -->
              <div class="form-group custom-grid-3 mt-3">
                <div>
                  <label for="department"></label>
                  <select class="form-control" id="department" v-model="department" required>
                    <option value="" disabled> </option>
                    <option value="IT">IT</option>
                    <option value="HR">HR</option>
                    <option value="Finance">Finance</option>
                  </select>
                </div>
                <div>
                  <label for="team"></label>
                  <select class="form-control" id="team" v-model="team" required>
                    <option value="" disabled> </option>
                    <option value="Team A">Team A</option>
                    <option value="Team B">Team B</option>
                    <option value="Team C">Team C</option>
                  </select>
                </div>
                <div>
                  <label for="position"></label>
                  <DefaultTextfield
                    id="position"
                    v-model="position"
                    size="full"
                    placeholder=" "
                    required
                  />
                </div>
              </div>

              <!--   -->
              <div class="form-group mt-3">
                <label for="zonecode"></label>
                <div class="input-group">
                  <DefaultTextfield
                    id="zonecode"
                    v-model="zonecode"
                    size="full"
                    placeholder=""
                    :disabled="true"
                    required
                  />
                  <div class="input-group-append">
                    <DefaultButton
                      color="gray"
                      @click="openModal"
                    >
                       
                    </DefaultButton>
                  </div>
                </div>
              </div>

              <div class="form-group mt-3">
                <label for="address"></label>
                <DefaultTextfield
                  id="address"
                  v-model="address"
                  size="full"
                  placeholder=" "
                  :disabled="true"
                  required
                />
              </div>

              <!--   -->
              <div class="form-group mt-3">
                <label for="detailAddress"> </label>
                <DefaultTextfield
                  id="detailAddress"
                  v-model="detailAddress"
                  size="full"
                  placeholder="  "
                />
              </div>

              <!--   -->
              <div class="form-group">
                <div class="btn-container">
                  <DefaultButton
                    size="large"
                    @click="handleSubmit">
                    
                  </DefaultButton>
                  <DefaultButton
                    size="large"
                    color="gray"
                    @click="onGoBack">
                    
                  </DefaultButton>
                </div>
              </div>

            </form>
          </div>
        </div>
      </div>
    </div>
    <KakaoAddressModal
      :visible="isModalOpen"
      @close="closeModal"
      @selectAddress="handleSelectAddress"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import DefaultTextfield from '@/components/common/textfield/DefaultTextfield.vue';
import KakaoAddressModal from '@/components/common/kakao/address/KakaoAddressModal.vue';

//   
const router = useRouter();

const userName = ref('');
const email = ref('');
const phone = ref('');
const dob = ref('');
const joinDate = ref('');
const department = ref('');
const team = ref('');  //    
const position = ref('');
const password = ref('');
const confirmPassword = ref('');

//   
const verificationCode = ref('');
const verificationCodeDisable = ref(false);

/**
 *   API
 */
const zonecode = ref('');
const address = ref('');
const detailAddress = ref('');
const isModalOpen = ref(false);

function openModal() {
  isModalOpen.value = true;
}
function closeModal() {
  isModalOpen.value = false;
}
function handleSelectAddress(payload) {
  // payload = { address: ..., zonecode: ... }
  address.value = payload.address;
  zonecode.value = payload.zonecode;
}

/**
 *      
 *  -  email    &   
 */
async function sendEmailVerification() {
  if (!email.value) {
    alert(' .');
    return;
  }

  try {
    //  API  (POST /api/auth/sendVerificationCode)
    //    API   
    const response = await fetch('/api/auth/sendVerificationCode', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: email.value })
    });
    if (response.ok) {
      alert('  .  !');
    } else {
      alert('   .');
    }
  } catch (error) {
    console.error('   :', error);
  }
}

/**
 *     
 *  -    
 */
async function sameVerification() {
  try {
    //  API  (POST /api/auth/sendVerificationCode)
    //    API   
    const response = await fetch('/api/auth/sendVerificationCode', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: email.value })
    });
    if (response.ok) {
      verificationCodeDisable.value = true;
    } else {
      alert('   .');
    }
  } catch (error) {
    console.error('   :', error);
  }
}

/**
 *    
 *  -     
 */
const formRef = ref(null);
const handleSubmit = () => {
  // formRef     .
  if (!formRef.value.checkValidity()) {
    formRef.value.reportValidity();
    return;
  }

  if (password.value !== confirmPassword.value) {
    alert('  .');
    return;
  }

  if (!verificationCode.value) {
    alert(' .');
    return;
  }

/**
 * 
 * 
 * 
 * 
 *  verificationCode     
 *       
 * 
 * 
 * 
 * 
 */
  //     
  console.log(' :', {
    userName: userName.value,
    email : email.value,
    phone: phone.value,
    dob: dob.value,
    joinDate: joinDate.value,
    department: department.value,
    team: team.value, //   
    position: position.value,
    address: address.value,
    detailAddress: detailAddress.value,
    password: password.value,
    verificationCode: verificationCode.value
  });
};

const onGoBack = () => {
  router.push('login');
};
</script>

