}

.btn-container {
  display: flex; /*    */
  justify-content: center; /*      */
  gap: 0.5rem; /*     */
  width: 100%; /*     */
}

/* Custom Grid Layout */
.custom-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px; /*    */
}

#zonecode {
  max-width: 30%; /*    50%  */
}

/* , ,    3  */
.custom-grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px; /*    */
}

/*         */
@media (max-width: 1200px) {
