<template>
  <nav class="navbar navbar-light">
    <router-link to="/">
      <a class="navbar-brand" href="/hrm/user-information">
        <img src="https://www.kpcnc.co.kr/src/img/logo.png" class="d-inline-block align-top" alt="Logo">
      </a>
    </router-link>
    <div class="navbar-text">
      <router-link to="/hrm/user-information" class="user-name">
        <img
          src="https://avatars.githubusercontent.com/u/48048508?v=4"
          class="rounded-circle user-image"
          alt="User Image"
        >
          
      </router-link>
      <div>&nbsp;|&nbsp;</div>
      <!--   -   doLogout()  -->
      <a href="#" class="user-name" @click.prevent="doLogout">
        
      </a>
      <i class="fas fa-caret-down user-icon"></i> <!--   -->
    </div>
  </nav>
