import axios from 'axios';
import router from '@/router';
// import { useLoadingStore } from '@/store/loading'; // <-  import  

/**
 *  
        import { authApi } from '@/api/apiConfig';

        export const login = (credentials) => {
        return hrmApi.post('/login', credentials);
        };
 */

// SYSTEM API 
const systemApi = axios.create({
  baseURL: process.env.VUE_APP_SYSTEM_API_BASE_URL, // .env   RECEIPT API  URL
  withCredentials: true, //   (HttpOnly  JWT )
  headers: {
    'Content-Type': 'application/json',
  },
});

// AUTH API 
const authApi = axios.create({
  baseURL: process.env.VUE_APP_AUTH_API_BASE_URL, // .env   AUTH API  URL
  withCredentials: true, //   
  headers: {
    'Content-Type': 'application/json',
  },
});

// HRM API 
const hrmApi = axios.create({
  baseURL: process.env.VUE_APP_HRM_API_BASE_URL, // .env   HRM API  URL
  withCredentials: true, //   
  headers: {
    'Content-Type': 'application/json',
  },
});

// RECEIPT API 
const receiptApi = axios.create({
  baseURL: process.env.VUE_APP_RECEIPT_API_BASE_URL, // .env   RECEIPT API  URL
  withCredentials: true, //   (HttpOnly  JWT )
  headers: {
    'Content-Type': 'application/json',
  },
});


//     
//    apiConfig.js useLoadingStore()  ( ) import  ,
//    addAuthInterceptor() import       
//     Pinia main.js app.use(createPinia())   store    
export function initInterceptors() {

  //  PINIA
  const { useLoadingStore } = require('@/store/loading'); 
  //  PINIA
  // const { useAuthStore } = require('@/store/auth');

  const loadingStore = useLoadingStore();
  // const authStore = useAuthStore();

  //    
  const addAuthInterceptor = (apiInstance) => {

    // 
    // (1) Request Interceptor
    // 
    apiInstance.interceptors.request.use(
      (config) => {
        //      
        loadingStore.startLoading();

        const currentRoute = router.currentRoute.value;
        const menu = currentRoute?.meta?.menu || "";
        // const user = authStore.user;

        // config.headers['X-Menu-Vue'] = encodeURIComponent(menu);
        // config.headers['X-User-Vue'] = encodeURIComponent(user);

        // path=/    (path) 
        document.cookie = "X-Menu-Vue=" + encodeURIComponent(menu) + "; path=/";

        // HttpOnly   ,     
        //    .
        return config;
      },
      (error) => {
        console.log('-----------------')
        console.log(error);
        console.log(error.response);
        //    ( )
        loadingStore.stopLoading();
        /**
         * 
         * 
         * 
         *    
         * 
         * 
         * 
         */
        if (error.response && error.response.status === 401) {
          alert("   ");
          router.push('/login');
        } else {
          //       
          console.log(error);
          alert(error);
        }
        return Promise.reject(error);
      }
    );

    // 
    // (2) Response Interceptor:   
    // 
    apiInstance.interceptors.response.use(
      (response) => {
        //       
        loadingStore.stopLoading();
        // response   
        // return response.data; //   
        return response;
      },
      (error) => {
        console.log(error);
        alert(error);
        //      
        loadingStore.stopLoading();
        /**
         * 
         * 
         * 
         *    
         * 
         * 
         * 
         */
        return Promise.reject(error);
      }
    );
  };
  
  addAuthInterceptor(systemApi);
  addAuthInterceptor(hrmApi);
  addAuthInterceptor(receiptApi);
  addAuthInterceptor(authApi);
}

export { systemApi, hrmApi, receiptApi, authApi };
