<template>
  <label :for="forId" :class="['default-label', 'mr-2', customClass]" :style="labelStyle">
    <slot>{{ text }}</slot>
    <span v-if="required" class="required-asterisk">*</span>
  </label>
</template>

<script setup>
import { computed, defineProps, ref, onMounted, onBeforeUnmount } from 'vue';

const props = defineProps({
  text: {
    type: String,
    default: ''
  },
  forId: {
    type: String,
    default: ''
  },
  required: {
    type: Boolean,
    default: false
  },
  customClass: {
    type: String,
    default: ''
  },
  // 'small', 'medium', 'large' 
  size: {
    type: String,
    default: 'medium',
    validator: (value) => ['small', 'medium', 'large'].includes(value)
  },
  color: {
    type: String,
    default: '#333'
  }
});

//    
const windowWidth = ref(window.innerWidth);
const updateWidth = () => {
  windowWidth.value = window.innerWidth;
};

onMounted(() => {
  window.addEventListener('resize', updateWidth);
});
onBeforeUnmount(() => {
  window.removeEventListener('resize', updateWidth);
});

//    (rem )  (props.size )
const baseFontSize = computed(() => {
  switch(props.size) {
    case 'small':
      return 0.8;
    case 'large':
      return 1.2;
    case 'medium':
    default:
      return 1.0;
  }
});

//   1200px    90% 
const labelStyle = computed(() => {
  const factor = windowWidth.value <= 1200 ? 0.9 : 1;
  return {
    fontSize: `${baseFontSize.value * factor}rem`,
    color: props.color,
    textAlign: 'center'
  };
});
</script>

<style scoped>
.default-label {
  display: inline-block;
}

.required-asterisk {
  color: red;
  margin-left: 0.25rem;
}
</style>
