<template>
  <!-- v-if="visible"   /  -->
  <div v-if="visible" class="modal-overlay">
    <div class="modal-container">
      <!--   -->
      <div class="modal-header">
        <h5 class="modal-title"> </h5>
        <button type="button" class="btn-close" @click="closeModal" />
      </div>

      <!--   -->
      <div class="modal-body">
        <form @submit.prevent="submitForm">
          <!-- ,  -->
          <div class="d-flex">
            <div class="form-group w-50 me-2">
              <label for="fromDate"></label>
              <input
                type="date"
                class="form-control"
                id="fromDate"
                v-model="localFormData.fromDate"
              />
            </div>
            <div class="form-group w-50">
              <label for="toDate"></label>
              <input
                type="date"
                class="form-control"
                id="toDate"
                v-model="localFormData.toDate"
              />
            </div>
          </div>
          <br />
          <!-- ,  -->
          <div class="d-flex">
            <div class="form-group me-2" style="flex: 4 3;">
              <label for="workType"></label>
              <select
                class="form-control"
                id="workType"
                v-model="localFormData.workType"
              >
                <option></option>
                <option></option>
                <option> </option>
              </select>
            </div>
            <div class="form-group" style="flex: 4 1;">
              <label for="status"></label>
              <select
                class="form-control"
                id="status"
                v-model="localFormData.status"
              >
                <option> </option>
                <option></option>
                <option> </option>
              </select>
            </div>
          </div>
          <br />
          <!--   -->
          <div class="form-group">
            <label for="performanceTitle"> </label>
            <input
              type="text"
              class="form-control"
              id="performanceTitle"
              placeholder="  "
              v-model="localFormData.performanceTitle"
            />
          </div>
          <br />
          <!--  -->
          <div class="form-group">
            <label for="performance"></label>
            <textarea
              class="form-control"
              id="performance"
              placeholder=" "
              v-model="localFormData.performance"
              rows="4"
            ></textarea>
          </div>
          <br />
          <!--   -->
          <div class="modal-footer">
            <DefaultButton 
              align="right"
              color="gray"
              @click="closeModal"
            >
              
            </DefaultButton>
            <DefaultButton 
              align="right"
              @click="submitForm"
            >
              
            </DefaultButton>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, watch, defineProps, defineEmits } from 'vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';

const props = defineProps({
  visible: { type: Boolean, default: false },
  formData: {
    type: Object,
    default: () => ({
      fromDate: '',
      toDate: '',
      workType: '',
      performanceTitle: '',
      performance: '',
      status: ' ',
    }),
  },
});
const emits = defineEmits(['close', 'submit']);

/**
 *     
 * =>   formData  
 */
const localFormData = ref({ ...props.formData });

/**
 * props.formData   localFormData  
 */
watch(
  () => props.formData,
  (newVal) => {
    localFormData.value = { ...newVal };
  },
  { deep: true }
);

/**   */
function closeModal() {
  emits('close');
}

/**  () */
function submitForm() {
  /** 
   * 
   *   ,     
   * 
   */
  // localFormData   emit
  emits('submit', { ...localFormData.value });
}
</script>

<style scoped>
/*     (Bootstrap JS   , so no 17px padding) */

/*   */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1050;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*   */
.modal-container {
  background: #fff;
  width: 100%;
  max-width: 600px;
  border-radius: 4px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  animation: fadeIn 0.3s;
  padding: 1rem;
}

/*  */
.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

/* ,  */
.modal-body {
  max-height: 70vh;
  overflow-y: auto;
}
.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
}

/*  .d-flex, .form-control, .me-2  Bootstrap   mimic */
.d-flex {
  display: flex;
  gap: 0.5rem;
}
.w-50 {
  width: 50%;
}
.form-control {
  width: 100%;
  padding: 0.4rem;
  margin: 0;
}
.me-2 {
  margin-right: 0.5rem;
}

@media (max-width: 1200px) {
  .modal-container {
    width: 80% !important;
  }
}

/*  fadeIn  */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
</style>
