// src/router/index.js
// import { createRouter, createWebHistory } from 'vue-router';
import { createRouter, createWebHashHistory } from 'vue-router';
import { useAuthStore } from '@/store/auth';  // Pinia 
import { useLoadingStore } from '@/store/loading'; // Pinia 
import authRoutes from './auth';
import receiptRoutes from './receipt';
import hrmRoutes from './hrm';
import systemRoutes from './system';
import MainPage from '@/views/MainPage.vue';
import UserLogin from '@/views/UserLogin.vue';
import UserJoin from '@/views/UserJoin.vue';

/**
 *      : meta requiresAuth=false 
 * menu   
 */
const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage,
    meta: { 
      menu: ''
    },
  },
  {
    path: '/login',
    name: 'UserLogin',
    component: UserLogin,
    meta: { 
      isLoginPage: true,
      requiresAuth: false,
      menu: ''
    },
  },
  {
    path: '/join',
    name: 'UserJoin',
    component: UserJoin,
    meta: {
      isJoinPage: true,
      requiresAuth: false,
      menu: ''
    },
  },
  {
    path: '/auth',
    name: 'Auth',
    children: authRoutes,
  },
  {
    path: '/receipt',
    name: 'Receipt',
    children: receiptRoutes,
  },
  {
    path: '/hrm',
    name: 'Hrm',
    children: hrmRoutes,
  },
  {
    path: '/system',
    name: 'System',
    children: systemRoutes,
  },
];

const router = createRouter({
  // history: createWebHistory(),
  history: createWebHashHistory(), // (#) 
  routes,
});

//     
//   
router.beforeEach((to, from, next) => {

  // .env LOCALTEST true    
  if (process.env.VUE_APP_LOCALTEST === 'true') {
    return next();
  }

  const authStore = useAuthStore(); // Pinia
  // :     
  //    requiresAuth: false  
  const requiresAuth = !to.matched.some(record => record.meta.requiresAuth === false);

  // (1)   
  if (requiresAuth) {

    // 1-A)   
    if (!authStore.isAuthenticated) {
      //       
      alert(' !');
      return next({ name: 'UserLogin' });
    }

    // 1-B)   (allowedRoles   )
    const routeAllowedRoles = to.meta.allowedRoles; // : ['ROLE_MANAGER', 'ROLE_EMPLOYEE']
    if (routeAllowedRoles && routeAllowedRoles.length > 0) {
      //   
      const userRoles = authStore.roles; // : ['ROLE_EMPLOYEE', 'ROLE_MANAGER']

      //    
      const hasRole = userRoles.some(role => routeAllowedRoles.includes(role));
      if (!hasRole) {
        //     
        alert('    !');
        return next(false); //    (  )
      }
    }
  }
  
  // (2)   or   
  return next();
});

//     (  )
// Axios       
router.afterEach(() => {
  //      
  const loadingStore = useLoadingStore();
  loadingStore.stopLoading();
});

export default router;
