<template>
  <div>
    <!--    -->
    <div class="content">
      <h2 class="content-title"> </h2>
      <p class="content-sub-title">   </p>

      <div class="search-controls">
        <!-- ,    -->
        <div class="d-flex align-items-center department-group mt-2">
          <label for="department" class="mr-2"> :</label>
          <DefaultSelect
            id="department"
            v-model="selectedDepartment"
            :options="departmentsOptions"
            placeholder=" "
            @change="updateTeams"
            size="small"
          />
          <label for="team" class="mr-2 label-team"> :</label>
          <DefaultSelect
            id="team"
            v-model="selectedTeam"
            :options="teamsOptions"
            placeholder=" "
            size="small"
          />
        </div>
        <!--    -->
        <div class="d-flex align-items-center name-group mt-2">
          <label for="nameSearch" class="mr-2"> :</label>
          <input
            type="text"
            class="form-control w-auto"
            id="nameSearch"
            v-model="nameSearch"
            placeholder=" "
          />
          <DefaultButton
            @click="search"
            size="small">
            
          </DefaultButton>
        </div>
      </div>

      <hr/>

      <!--   ( 10  ) -->
      <DefaultTable
        :noColumns="true"
        :columns="columns"
        :data="filteredData"
        :rowClick="(item) => showModal(item)"
        :ellipsisWidth="50"
      />

      <!--       -->
      <UserManagementModifyModal
        :visible="modalVisible"
        :isAdmin="isAdmin"
        :user="selectedUser"
        @close="modalVisible = false"
        @confirmSave="handleSaveClick"
      />

      <!--    -->
      <AlertModal 
        :isVisible="confirmationModalVisible" 
        :disableBackgroundClose="true"
        title=" " 
        cancelText=""
        confirmText="" 
        @close="confirmationModalVisible = false" 
        @confirm="saveUserDetails"
      >
        <template #body>
          <p>  ?</p>
          <p v-if="statusChanged" style="color: red;">/   .</p>
        </template>
      </AlertModal>

      <!--       -->
      <AlertModal 
        :isVisible="noChangesModalVisible" 
        title="" 
        confirmText="" 
        @close="closeNoChangesModal"
        @confirm="closeNoChangesModal"
      >
        <template #body>
          <p>  .</p>
        </template>
      </AlertModal>

      <!--    -->
      <AlertModal 
        :isVisible="saveCompleteModalVisible" 
        :title="saveCompleteMessageTitle" 
        confirmText=""
        @close="closeSaveCompleteModal" 
        @confirm="closeSaveCompleteModal"
      >
        <template #body>
          <p>{{ saveCompleteMessage }}</p>
        </template>
      </AlertModal>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import AlertModal from '@/components/common/modal/AlertModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';
import UserManagementModifyModal from '@/components/hrm/UserManagementModifyModal.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import DefaultSelect from '@/components/common/select/DefaultSelect.vue';

//  
const isAdmin = ref(true);

//   
const modalVisible = ref(false);
const confirmationModalVisible = ref(false);
const saveCompleteModalVisible = ref(false);
const noChangesModalVisible = ref(false);
const saveCompleteMessageTitle = ref(' ');
const saveCompleteMessage = ref('.');

//  
const columns = [
  { key: 'name',       label: '', width: 60 },
  { key: 'email',      label: '', width: 160 }, 
  { key: 'department', label: '', width: 100 }, 
  { key: 'team',       label: '', width: 100 },
  { key: 'position',   label: '', width: 60 },
  { key: 'status',     label: '', width: 50 },
];

//     
const selectedUser = ref({
  name: '',
  email: '',
  password: '',
  department: '',
  team: '',
  position: '',
  joinDate: '',
  leaveDate: '',
  status: '',
  birthDate: '',
  employmentType: '',
  zipCode: '',
  address: '',
  location: ''
});
const originalUser = ref({ ...selectedUser.value });

//    (/ )
const statusChanged = ref(false);
//   
const dataChanged = ref(false);

//      
const selectedDepartment = ref('');
const selectedTeam = ref('');
const departments = ref([]);
const teams = ref({});
const availableTeams = ref([]);

// ,   
const fetchDepartments = async () => {
  try {
    //    
    const departmentData = [
      {
        "name": "",
        "teams": [""]
      },
      {
        "name": "SI",
        "teams": ["", ""]
      },
      {
        "name": "AI",
        "teams": ["RPA"]
      },
      {
        "name": "",
        "teams": ["", ""]
      }
    ];

    departments.value = departmentData.map(dep => dep.name);
    teams.value = departmentData.reduce((acc, dep) => {
      acc[dep.name] = dep.teams;
      return acc;
    }, {});
  } catch (error) {
    console.error('Error fetching departments:', error);
  }
};

//     
const updateTeams = () => {
  availableTeams.value = teams.value[selectedDepartment.value] || [];
  selectedTeam.value = '';
};

//   (DefaultSelect - computed)
//   { value, label }      
const departmentsOptions = computed(() => {
  return [{ value: '', label: ' ' }, ...departments.value.map(dep => ({ value: dep, label: dep }))];
});

//   (DefaultSelect - computed)
const teamsOptions = computed(() => {
  return [{ value: '', label: ' ' }, ...availableTeams.value.map(team => ({ value: team, label: team }))];
});

//  ()
const data = ref([
  { name: '', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI', team: '', position: '', joinDate: '2022-01-01', leaveDate: '', status: '', birthDate: '1990-01-01', employmentType: '', zipCode: '12345', address: ' ', location: '' },
  { name: '', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI', team: '', position: '', joinDate: '2022-01-01', leaveDate: '', status: '', birthDate: '1990-01-01', employmentType: '', zipCode: '12345', address: ' ', location: '' },
  { name: '', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI', team: '', position: '', joinDate: '2022-01-01', leaveDate: '', status: '', birthDate: '1990-01-01', employmentType: '', zipCode: '12345', address: ' ', location: '' },
  { name: '', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI', team: '', position: '', joinDate: '2022-01-01', leaveDate: '', status: '', birthDate: '1990-01-01', employmentType: '', zipCode: '12345', address: ' ', location: '' },
  { name: '', email: 'kim@example.com', password: '1237', department: 'AI', team: 'RPA', position: '', joinDate: '2021-07-15', leaveDate: '2023-07-11', status: '', BirthDate: '1985-06-15', employmentType: '', zipCode: '67890', address: ' ', location: '' },
  { name: '', email: 'kim@example.com', password: '1237', department: 'AI', team: 'RPA', position: '', joinDate: '2021-07-15', leaveDate: '2023-07-11', status: '', BirthDate: '1985-06-15', employmentType: '', zipCode: '67890', address: ' ', location: '' },
  { name: '', email: 'lee@example.com', password: '1234', department: '', team: '', position: '', joinDate: '2023-03-12', leaveDate: '', status: '', BirthDate: '1995-03-12', employmentType: '', zipCode: '54321', address: ' ', location: '' },
  { name: '', email: 'lee@example.com', password: '1234', department: '', team: '', position: '', joinDate: '2023-03-12', leaveDate: '', status: '', BirthDate: '1995-03-12', employmentType: '', zipCode: '54321', address: ' ', location: '' },
  { name: '', email: 'lee@example.com', password: '1234', department: '', team: '', position: '', joinDate: '2023-03-12', leaveDate: '', status: '', BirthDate: '1995-03-12', employmentType: '', zipCode: '54321', address: ' ', location: '' },
  { name: '', email: 'su@example.com', password: '1234', department: 'SI', team: '', position: '', joinDate: '2024-07-12', leaveDate: '', status: '', BirthDate: '1992-03-12', employmentType: '', zipCode: '7777', address: '', location: '' },
  { name: '', email: 'su@example.com', password: '1234', department: 'SI', team: '', position: '', joinDate: '2024-07-12', leaveDate: '', status: '', BirthDate: '1992-03-12', employmentType: '', zipCode: '7777', address: '', location: '' },
  { name: '', email: 'su@example.com', password: '1234', department: 'SI', team: '', position: '', joinDate: '2024-07-12', leaveDate: '', status: '', BirthDate: '1992-03-12', employmentType: '', zipCode: '7777', address: '', location: '' },
  { name: '', email: 'su@example.com', password: '1234', department: 'SI', team: '', position: '', joinDate: '2024-07-12', leaveDate: '', status: '', BirthDate: '1992-03-12', employmentType: '', zipCode: '7777', address: '', location: '' },
]);

// 
const nameSearch = ref('');

//  
const filteredData = computed(() => {
  return data.value.filter(item => {
    const departmentMatch = selectedDepartment.value
      ? item.department === selectedDepartment.value
      : true;
    const teamMatch = selectedTeam.value
      ? item.team === selectedTeam.value
      : true;
    const nameMatch = nameSearch.value
      ? item.name.includes(nameSearch.value)
      : true;
    return departmentMatch && teamMatch && nameMatch;
  });
});

// 
const search = () => {
  //   currentPage=1    
  //   
};

//     
const showModal = (user) => {
  selectedUser.value = { ...user };
  originalUser.value = { ...user };
  statusChanged.value = false;
  dataChanged.value = false;
  modalVisible.value = true;
};

//    (  @confirmSave )
const handleSaveClick = ({ isMismatch, isChanged, userData, statusChangedFlag }) => {
  //   
  if (isMismatch) {
    saveCompleteMessageTitle.value = ' ';
    saveCompleteMessage.value = '    .';
    saveCompleteModalVisible.value = true;
    return;
  }

  //    
  if (!isChanged) {
    noChangesModalVisible.value = true;
    return;
  }

  // userData   selectedUser 
  selectedUser.value = { ...userData };

  //     (/)
  statusChanged.value = statusChangedFlag;

  //     
  confirmationModalVisible.value = true;
};

//  DB  
const saveUserDetails = () => {
  //  
  modalVisible.value = false;
  confirmationModalVisible.value = false;

  axios.post('/api/saveUser', selectedUser.value)
    .then(response => {
      console.log(response.data);
      //  
      saveCompleteMessageTitle.value = ' ';
      saveCompleteMessage.value = '.';
      saveCompleteModalVisible.value = true;
    })
    .catch(error => {
      console.log(error);
      //  
      saveCompleteMessageTitle.value = ' ';
      saveCompleteMessage.value = ' .( )';
      saveCompleteModalVisible.value = true;
    });
};

// '  '  
const closeNoChangesModal = () => {
  noChangesModalVisible.value = false;
};

//    
const closeSaveCompleteModal = () => {
  saveCompleteModalVisible.value = false;
};

// onMounted    
onMounted(() => {
  fetchDepartments();
});
</script>

<style scoped>
hr {
  margin-bottom: 0px;
}

/*    */
.label-team {
  margin-left: 10px;
}

/*   */
.table {
  width: 100%;
  margin: 0; /*   .table-container   */
}
  
/*     10     ( 350~400px) */
.table-container {
  max-height: 400px; /*    */
  overflow-y: auto;
  margin-bottom: 60px;
}

@media (min-width: 1201px) {
  .search-controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
  .search-controls > div {
    margin-right: 1rem;
    margin-bottom: 0;
  }
  #nameSearch {
    font-size: 1rem;
  }
}

@media (max-width: 1200px) {
  .search-controls {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .search-controls > div {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
}
</style>
