<template>
  <div class="default-textfield">
    <!--
      :type, :id, :name, :placeholder
               input   

      v-model="internalValue"
           ,    internalValue ,  modelValue 

      v-bind="type === 'radio' ? { value: value } : {}"
         input  "radio"  value prop ,    

      :class="..."
         input  "radio"   'form-control',   (sizeClass), 
              'is-invalid'  
          "radio"      (  ) 

      @blur, @change
         blur  touched true , change   emit      

      :disabled, :required
          (disabled)   (required)      
    -->
    <input
      :type="type"
      :id="id"
      :name="name"
      :placeholder="placeholder"
      v-model="internalValue"
      v-bind="type === 'radio' ? { value: value } : {}"
      :class="type !== 'radio' ? ['form-control', sizeClass, { 'is-invalid': touched && !isValid }] : ''"
      @blur="touched = true"
      @change="$emit('change', $event)"
      :disabled="disabled"
      :required="required"
    />
    <!--   : reserveErrorSpace       -->
    <template v-if="reserveErrorSpace">
      <!--        -->
      <div class="error-message reserved">
        <span v-if="touched && !isValid">{{ errorMessage }}</span>
      </div>
    </template>
    <template v-else>
      <!--      -->
      <div v-if="touched && !isValid" class="error-message">
        {{ errorMessage }}
      </div>
    </template>
  </div>
</template>

<script setup>
import { computed, defineProps, defineEmits, ref } from 'vue';

const props = defineProps({
  // v-model  
  //  Date    ,   
  modelValue: {
    type: [String, Date],
    default: ''
  },
  // input   
  //  , "text", "date", "month", "radio"      
  //  "text"
  type: {
    type: String,
    default: 'text'
  },
  // input  id 
  //  label for    DOM  
  id: {
    type: String,
    default: ''
  },
  // input  placeholder( ) 
  //         
  placeholder: {
    type: String,
    default: ''
  },
  // input    
  // 'small', 'medium', 'large', 'full'    ,
  //    CSS (: small-input, medium-input ) 
  size: {
    type: String,
    default: 'medium',
    validator: value => ['xsamll', 'small', 'medium', 'large', 'full'].includes(value)
  },
  /**
   * validationType :
   *    
   *     
   * - ''         :  
   * - 'english'  :   
   * - 'number'   :   
   * - 'korean'   :   
   * - 'email'    :    (: user@example.com)
   */
  validationType: {
    type: String,
    default: '',
    validator: value => ['', 'english', 'number', 'korean', 'email'].includes(value)
  },
  // validationType       
  //   "   ." 
  errorMessage: {
    type: String,
    default: '   .'
  },
  /**
   * reserveErrorSpace :
   * true,        
   * false,        
   *          
   */
  reserveErrorSpace: {
    type: Boolean,
    default: false
  },
  // disabled :
  // true ,          
  disabled: {
    type: Boolean,
    default: false
  },
  // required :
  // true , HTML5 required      
  required: {
    type: Boolean,
    default: false
  },
  // name :
  //           
  //       name   
  name: {
    type: String,
    default: ''
  },
  // value :
  //     ,     
  // [String, Number, Boolean]  
  value: {
    type: [String, Number, Boolean],
    required: false
  }
});


const emits = defineEmits(['update:modelValue', 'change']);

// v-model    computed
const internalValue = computed({
  get() {
    return props.modelValue;
  },
  set(newValue) {
    emits('update:modelValue', newValue);
  }
});

// size    
const sizeClass = computed(() => {
  switch (props.size) {
    case 'xsmall':
      return 'xsmall-input';
    case 'small':
      return 'small-input';
    case 'large':
      return 'large-input';
    case 'full':
      return 'full-input';
    case 'medium':
    default:
      return 'medium-input';
  }
});

// validationType   
const pattern = computed(() => {
  switch (props.validationType) {
    case 'english':
      return /^[A-Za-z]*$/;
    case 'number':
      return /^[0-9]*$/;
    case 'korean':
      return /^[--]*$/;
    case 'email':
      return /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
    default:
      return null;
  }
});

//     (   true)
const isValid = computed(() => {
  if (!pattern.value) return true;
  return pattern.value.test(internalValue.value);
});

//    (blur  true )
const touched = ref(false);
</script>

<style scoped>
.xsmall-input {
  width: 130px;
  height: 2rem;
  font-size: 0.8rem; 
}

.small-input {
  width: 160px;
  font-size: 0.8rem;
}

.medium-input {
  width: 300px;
}

.large-input {
  width: 500px;
}

.full-input {
  width: 100%;
}

.xsmall-input::placeholder {
  font-size: 0.85rem;
}

.small-input::placeholder {
  font-size: 0.9rem;
}

.medium-input::placeholder {
  font-size: 1rem;
}

.large-input::placeholder {
  font-size: 1.05rem;
}

.full-input::placeholder {
  font-size: 1rem;
}

/*       */
.is-invalid {
  border-color: red;
}

/*   : reserveErrorSpace true   */
.error-message.reserved {
  min-height: 1.2rem; /*     (   ) */
  margin-top: 5px;
  color: red;
  font-size: 0.85rem;
}

/* reserveErrorSpace false   error-message  */
.error-message {
  margin-top: 5px;
  color: red;
  font-size: 0.85rem;
}

@media (max-width: 1200px) {

  .default-textfield input[type="month"],
  .default-textfield input[type="date"] {
    min-width: 110px;
  }

  /*   input   */
  .xsmall-input { 
    width: 80px;
  }

  .small-input {
    width: 130px;
  }

  .medium-input {
    width: 220px;
  }

  .large-input {
    width: 280px;
  }

  .xsmall-input::placeholder {
    font-size: 0.75rem;
  }

  .small-input::placeholder {
    font-size: 0.8rem;
  }

  .medium-input::placeholder {
    font-size: 0.9rem;
  }

  .large-input::placeholder {
    font-size: 0.95rem;
  }

  .full-input::placeholder {
    font-size: 1rem;
  }
}

@media (max-width: 500px) {

  .default-textfield input[type="month"],
  .default-textfield input[type="date"] {
    min-width: 90px;
  }

  /*   input   */
  .xsmall-input {
    width: 60px;
  }

  .small-input {
    width: 98px;
  }

  .medium-input {
    width: 150px;
  }

  .large-input {
    width: 180px;
  }

  .default-textfield input[type="month"] {
    font-size: 0.6rem !important; /*    */
  }

  .default-textfield input[type="date"] {
    font-size: 0.6rem !important; /*    */
  }

  .xsmall-input::placeholder {
    font-size: 0.55rem;
  }

  .small-input::placeholder {
    font-size: 0.65rem;
  }

  .medium-input::placeholder {
