<template>
  <div>
    <!--    -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title"> </h2>
      <p class="content-sub-title">     </p>

      <div class="d-flex justify-content-end align-items-center mb-3">
        <label for="monthSearch" class="mr-2">  :</label>
        <select class="form-control w-auto" id="monthSearch" v-model="selectedMonth" @change="filterByMonth">
          <option value="01">1</option>
          <option value="02">2</option>
          <option value="03">3</option>
          <option value="04">4</option>
          <option value="05">5</option>
          <option value="06">6</option>
          <option value="07">7</option>
          <option value="08">8</option>
          <option value="09">9</option>
          <option value="10">10</option>
          <option value="11">11</option>
          <option value="12">12</option>
        </select>
      </div>

      <!--   (  ) -->
      <DefaultTable
        :columns="columns"
        :data="data"
        :openSidebar="openSidebar"
        :showTable="!isMobile"
      />

      <!--   (  ) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="(item, index) in data" :key="index">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text">
              <strong> : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">
                {{ item.people.length }}
              </span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header"></div>
                <p 
                  v-for="person in item.people" 
                  :key="person.name" 
                  class="people-list-item"
                >
                  {{ person.name }} ({{ person.department }} - {{ person.team }})
                </p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>/ : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong> : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>/ : </strong> {{ item.amountPerPerson }}</p>
            <p>
              <strong class="card-text">  : </strong>
              <a class="card-text"
                @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;"
              >
                {{ item.receiptName }}
              </a>
            </p>
            <p class="card-text">
              <strong> : </strong> {{ item.rejectionReason || 'X' }}
            </p>
            <p class="card-text">
              <strong> : </strong>
              <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
          </div>
        </div>
      </div>

      <!--  ( ) -->
      <DefaultPagination
        :currentPage="currentPage"
        :totalPages="totalPages"
        :visiblePageCount="visiblePageCount"
        @pageChange="onPageChange"
      />
    </div>

    <!--   -->
    <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
      <ReceiptSidebar 
        v-if="!isMobile" 
        :item="selectedItem" 
        :visible="sidebarVisible" 
        @close="closeSidebar"
        @preview-image="openPreviewModal"
      />
    </div>

    <!--    -->
    <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
      <div
        class="preview-modal-content"
        @mousedown="startDrag"
        @mousemove="onDrag"
        @mouseup="endDrag"
        @mouseleave="endDrag"
        @touchstart="startDrag"
        @touchmove="onDrag"
        @touchend="endDrag"
      >
        <img
          :src="previewImage"
          :class="{ zoomed: isZoomed }"
          class="preview-modal-image"
          :style="{
            transform: isZoomed
              ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
              : 'none',
            transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
          }"
          @dblclick="toggleZoom"
          @touchstart="toggleZoom"
        />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import ReceiptSidebar from './ReceiptSidebar.vue';
import DefaultPagination from '@/components/common/pagination/DefaultPagination.vue';
import DefaultTable from '@/components/common/table/DefaultTable.vue';
import { usePreviewModal } from '@/utils/preview-modal';
// import axios from 'axios'; //    

// ==============================
//    
// ==============================
const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

function closePreviewModalOnOutsideClick(event) {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
}

// ==============================
//   /  
// ==============================
const data = ref([]); //     

/**
 * columns 
 * - key:   
 * - label:    
 * - customClass: ()     
 */
const columns = [
  { key: 'date', label: '', width: 100  },
  { key: 'type', label: '', width: 50  },
  { key: 'peopleCount', label: '', width: 60  },
  { key: 'reason', label: '', width: 200  },
  { key: 'amount', label: '', width: 60  },
  { key: 'amountPerPerson', label: '/', width: 100  },
  {
    key: 'status',
    label: ' ',
    width: 80,
    customClass: (value) => {
      if (value === '') return 'text-blue';
      if (value === '') return 'text-green';
      if (value === '') return 'text-red';
      return '';
    },
  },
];

// ==============================
//  
// ==============================
const currentPage = ref(1);       //  
const totalPages = ref(1);        //     
const visiblePageCount = ref(5);  //      

/**
 *        
 */
function onPageChange(newPage) {
  currentPage.value = newPage;
  fetchDataFromServer(newPage);
}

// : / 
function calculateAmountPerPerson(item) {
  const amountNum = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people?.length || 0;
  return peopleCount > 0
    ? Math.floor(amountNum / peopleCount).toLocaleString() + ''
    : 'N/A';
}

/**
 *     ( )
 * -  axios.get(...)   
 *    { page, selectedMonth }    ,
 *    data, totalPages    .
 */
// async function fetchDataFromServer(page = 1) {
async function fetchDataFromServer() {
  try {
    // const response = await axios.get('/api/receipts', {
    //   params: { page, month: selectedMonth.value },
    // });
    // data.value = response.data.items;
    // totalPages.value = response.data.totalPages;

    // --------------------------
    //      : mockData
    // --------------------------
    const mockData = [
      {
        date: '2024-08-01',
        type: '',
        reason: '',
        amount: '100,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        status: '',
        rejectionReason: '   ',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      {
        date: '2024-08-02',
        type: '',
        reason: '',
        amount: '110,000',
        receiptName: ' ',
        receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
        status: '',
        rejectionReason: '',
        people: [
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
          { department: 'SI', team: '', name: '', position: '' },
        ],
      },
      // page=2,3  mockData    
    ];

    //   
    data.value = mockData.map(item => ({
      ...item,
      peopleCount: item.people.length,
      amountPerPerson: calculateAmountPerPerson(item),
    }));

    totalPages.value = 3; // :     
  } catch (error) {
    console.error(error);
  }
}

// ==============================
//  /  
// ==============================
const isMobile = ref(window.innerWidth <= 1200);
function updateIsMobile() {
  isMobile.value = window.innerWidth <= 1200;
}
window.addEventListener('resize', updateIsMobile);

const selectedItem = ref(null);
const sidebarVisible = ref(false);
function openSidebar(item) {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
}
function closeSidebar() {
  sidebarVisible.value = false;
}

// ==============================
//  -> CSS   ()
// ==============================
function getStatusClass(status) {
  if (status === '') return 'status-pending';
  if (status === '') return 'status-approved';
  if (status === '') return 'status-rejected';
  return '';
}

// ==============================
//    
// ==============================
const visiblePeopleLists = ref({});
function togglePeopleList(item) {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
}
function showPeopleList(item) {
  return visiblePeopleLists.value[item.date];
}

// ==============================
//  
// ==============================
const selectedMonth = ref(getCurrentMonth());
function getCurrentMonth() {
  const now = new Date();
  const month = now.getMonth() + 1;
  return month < 10 ? `0${month}` : `${month}`;
}
function filterByMonth() {
  //  ( selectedMonth.value )  
  // fetchDataFromServer(1)  ,    
  currentPage.value = 1;
  fetchDataFromServer(1);
}

//    1  
onMounted(() => {
  fetchDataFromServer(currentPage.value);
});
</script>

<style scoped>
.content {
  transition: margin-right 0.3s ease;
}
.content-expanded {
  margin-right: 300px;
}

/*  */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}
.sidebar-visible {
  transform: translateX(0);
}
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

/*    */
.status-approved {
  color: green;
  font-weight: bold;
}
.status-pending {
  color: blue;
  font-weight: bold;
}
.status-rejected {
  color: red;
  font-weight: bold;
}

/* columns customClass   */
.text-red {
  color: red;
  font-weight: bold;
}
.text-blue {
  color: blue;
  font-weight: bold;
}
.text-green {
  color: green;
  font-weight: bold;
}

/*    */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}
.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}
.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

/*    */
.preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}
.preview-modal-content {
  position: relative;
}
.preview-modal-image {
  max-width: 80vw;
  max-height: 80vh;
  transition: transform 0.2s ease;
}
</style>
