// src/main.js
import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify'
import { BootstrapVue3 } from 'bootstrap-vue-3';
import { createPinia } from 'pinia';
import piniaPluginPersistedstate from 'pinia-plugin-persistedstate';
import { usePreviewModal } from './utils/preview-modal'; // Import the preview-modal composable
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-vue-3/dist/bootstrap-vue-3.css';
import './assets/styles/common.css'; //  CSS 
import './assets/styles/card.css'; //  CSS 
import './assets/styles/preview-modal.css'; //  CSS 
import { initInterceptors } from '@/api/apiConfig';
// import './assets/styles/font.css'; //  Font CSS 
// import axios from 'axios';


//   
// import '@/styles/mobile.css'; 

const app = createApp(App);

// Register the preview modal globally
app.config.globalProperties.$previewModal = usePreviewModal();

const pinia = createPinia();
pinia.use(piniaPluginPersistedstate);
app.use(pinia);

app.use(router);
app.use(vuetify);
app.use(BootstrapVue3);

initInterceptors();

app.mount('#app');
