<script setup>
import { ref, computed } from 'vue';
import { useAuthStore } from '@/store/auth'; // Pinia 

// 1) Pinia roles 
const authStore = useAuthStore();
const roles = computed(() => authStore.roles);

// 2) Vue CLI  .env VUE_APP_LOCALTEST  
//    => .env  VUE_APP_LOCALTEST=true     'true' 
const isLocaltest = computed(() => process.env.VUE_APP_LOCALTEST === 'true');

// 3)   (  + local test )
const hasAnyRole = (allowedRoles) => {
  return allowedRoles.some((role) => roles.value.includes(role));
};

// localtest  or ROLE_SYSTEM   true,   role 
const canShow = (allowedRoles) => {
  if (isLocaltest.value) {
    return true;
  }
  if (roles.value.includes('ROLE_SYSTEM')) {
    return true;
  }
  return hasAnyRole(allowedRoles);
};

//    (  )
const menus = ref({
  receipt: false,
  user: false,
  management: false,
  organization: false,
});

const isMobile = ref(window.innerWidth <= 1200);
const isSidebarOpen = ref(false);

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1200;
};

const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value;
  if (isSidebarOpen.value) {
    document.querySelector('.mobile-menu-toggle').style.top = '85px';
  } else {
    document.querySelector('.mobile-menu-toggle').style.top = '90px';
  }
};

const closeSidebar = () => {
  isSidebarOpen.value = false;
  const menuToggleButton = document.querySelector('.mobile-menu-toggle');
  if (menuToggleButton) {
    menuToggleButton.style.top = '90px';
  }
};

const toggleMenu = (menu) => {
  Object.keys(menus.value).forEach(key => {
    if (key !== menu) {
      menus.value[key] = false;
    }
  });
  menus.value[menu] = !menus.value[menu];
};

const handleMenuClick = () => {
  if (isMobile.value) {
    closeSidebar();
  }
};

// JavaScript transition 
const beforeEnter = (el) => {
  el.style.height = '0';
  el.style.opacity = '0';
};
const enter = (el, done) => {
  const height = el.scrollHeight;
  el.style.transition = 'all 0.3s ease';
  el.style.height = height + 'px';
  el.style.opacity = '1';
  el.addEventListener('transitionend', done);
};
const leave = (el, done) => {
  el.style.transition = 'all 0.3s ease';
  el.style.height = '0';
  el.style.opacity = '0';
  el.addEventListener('transitionend', done);
};

window.addEventListener('resize', updateIsMobile);
</script>

