<!--
   DefaultTable      

   :
  <DefaultTable
    :columns="[
      { key: 'name', label: '', width: 120 },
      { key: 'email', label: '', width: 200 },
      { key: 'department', label: '', width: 150 },
      { key: 'team', label: '', width: 150 }
    ]"
    :data="userData"
    :footerData="footerData"
    :showTable="true"
    :noColumns="false"
    :rowClick="handleRowClick"
    :openSidebar="openSidebar"
    :bodyFontSize="'0.875rem'"
    :calculateAmountPerPerson="(item) => item.amount"
  />

   :
  - columns:    .
              :
        - key:   .
        - label:   .
        - width:     ().     150px .
        - customClass:       ().
        - customValue:      ().

  - data:    .   columns key    .
  - footerData:       ().
  - openSidebar:        ().
  - showTable:    (true ).
  - rowClick:        ().
  - noColumns:     (false  ).
  - bodyFontSize:     (CSS ,  '0.875rem').
  - calculateAmountPerPerson:       (,  item.amount).
-->
<template>
  <div class="table-container">
    <table v-if="showTable" class="table" :style="{ '--table-body-font-size': bodyFontSize }">
      <thead v-if="!noColumns">
        <tr>
          <th v-for="column in columns" :key="column.key">
            {{ column.label }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(item, rowIndex) in data"
          :key="rowIndex"
          @click="handleRowClick(item)"
        >
          <td
            v-for="column in columns"
            :key="column.key"
            :class="[ column.customClass ? column.customClass(item[column.key]) : '', 'ellipsis' ]"
            :style="columnStyles(column)"
          >
            {{ column.customValue ? column.customValue(item) : item[column.key] }}
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td v-for="(footerItem, index) in footerData" :key="index">
            {{ footerItem }}
          </td>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script setup>
import { defineProps } from 'vue';

const props = defineProps({
  //    
  columns: {
    type: Array,
    required: false
  },
  //    .
  //    key   .
  data: {
    type: Array,
    required: true
  },
  //     
  footerData: {
    type: Array,
    required: false,
    default: () => []
  },
  //        ()
  openSidebar: {
    type: Function,
    required: false
  },
  //     .
  // true  .
  showTable: {
    type: Boolean,
    required: false,
    default: true
  },
  //        ()
  rowClick: {
    type: Function,
    required: false,
    default: null
  },
  //    .
  // false   .
  noColumns: {
    type: Boolean,
    required: false,
    default: false
  },
  //   
  bodyFontSize: {
    type: String,
    required: false,
    default: '0.875rem'
  },
  //       ()
  calculateAmountPerPerson: {
    type: Function,
    required: false,
    default: (item) => item.amount
  },
});

const columnStyles = (column) => {
  const style = {};
  //  width     ,   150px 
  const fixedWidth = column.width || 150;
  style.width = fixedWidth + 'px';
  style.minWidth = fixedWidth + 'px';
  style.maxWidth = fixedWidth + 'px';
  
  return style;
};

const handleRowClick = (item) => {
  if (props.openSidebar) {
    props.openSidebar(item);
  }
  if (props.rowClick) {
    props.rowClick(item);
  }
};
</script>

<style scoped>
.table-container {
  width: 100%;
  overflow-x: auto;
}

.table {
  border-collapse: collapse;
  width: 100%;
}

/* th, td      */
.table th,
.table td {
  padding: 8px;
  /* border-top: 1px solid #ccc; */
  /* border-bottom: 1px solid #ccc; */
}

/*      ,     ( )  */
.table th:not(:first-child),
.table td:not(:first-child) {
  border-left: 1px solid #e0e0e0;
}

/* th     (2px solid) */
.table th {
  border-top: 1px solid #a5a5a5;
  border-bottom: 1px solid #a5a5a5;
}

/*  ellipsis  (  ... ) */
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/*     */
tbody {
  font-size: var(--table-body-font-size, 1rem);
}

/*    () */
.table-container table tbody td {
  color: rgb(27, 27, 27) !important;
}

/*      */
.text-red {
  color: red;
  font-weight: bold;
}
.text-black {
  color: black;
}
.text-blue {
  color: blue;
}
.text-green {
  color: green;
}

/*  Hover   */
tbody tr:hover td {
  background-color: #e7f1ff !important;
}

@media (max-width: 1200px) {
  .table {
    font-size: 0.75rem !important;
  }
  .table-container table tbody td {
    font-size: 0.75rem;
  }
  .table th,
  .table td {
    padding: 6px;
  }
}

@media (max-width: 700px) {
  .table {
    font-size: 0.5rem !important;
  }
  .table-container table tbody td {
    font-size: 0.5rem;
  }
  .table th,
  .table td {
    padding: 5px;
  }
}
</style>
