<template>
  <div :class="rowClass">
    <slot></slot>
  </div>
</template>

<script setup>
import { computed, defineProps } from 'vue';

const props = defineProps({
  //  : 'left'(), 'center', 'right'
  align: {
    type: String,
    default: 'left',
    validator: value => ['left', 'center', 'right'].includes(value)
  },
  //   
  customClass: {
    type: String,
    default: ''
  }
});

// align prop    .
const rowClass = computed(() => {
  let alignmentClass = '';
  switch (props.align) {
    case 'center':
      alignmentClass = 'justify-content-center';
      break;
    case 'right':
      alignmentClass = 'justify-content-end';
      break;
    case 'left':
    default:
      alignmentClass = 'justify-content-start';
  }
  //  : d-flex, align-items-center, mt-2  .
  return `default-form-row d-flex align-items-center mt-2 ${alignmentClass} ${props.customClass}`.trim();
});
</script>

<style scoped>
.default-form-row {
  width: 100%;
}
</style>
